import { useState } from 'react';
import styled from 'styled-components';
import GuideDelivered from '../components/GuideDelivered';
import GuideInTransit from '../components/GuideInTransit';
import GuideInWarehouse from '../components/GuideInWarehouse';
import GuideRegistration from '../components/GuideRegistration';
import GuideReturned from '../components/GuideReturned';
import GuideReturnRegistration from '../components/GuideReturnRegistration';
import GuideReturnToCustomer from '../components/GuideReturnToCustomer';
import GuideTracking from '../components/GuideTracking';
import Layout from "../components/Layout";

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const NavContainer = styled.div`
    width: 90vw;

    overflow-x: scroll;
    ::-webkit-scrollbar {display: none;}
`;

const NavApp = styled.div`
    display: flex;
    align-items: center;
    justify-contetn: space-around;
`;

const NavElement = styled.button`
    background: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-weight: ${props => props.isActive ? "bold;" : "initial;"}

    &:hover {
        font-weight: bold;
    }
`;

const ordersOptions = [
    {id:1,tittle:'ENTRADA DE GUIAS',value:'ENTRADA'},
    {id:2,tittle:'DEVOLUCION DE GUIAS',value:'DEVOLUCION'},
    {id:3,tittle:'EN ALMACEN',value:'ALMACEN'}, 
    {id:4,tittle:'EN TRANSITO',value:'TRANSITO'},
    {id:5,tittle:'ENTREGADAS',value:'ENTREGADAS'}, 
    {id:6,tittle:'POR DEVOLVER',value:'DEVUELTACLIENTE'},
    {id:7,tittle:'DEVUELTAS',value:'DEVUELTAS'},
    {id:8,tittle:'SEGUIMIENTO',value:'SEGUIMIENTO'}
]

const opciones = {};
opciones["ENTRADA"] = <GuideRegistration />
opciones["DEVOLUCION"] = <GuideReturnRegistration />
opciones["ALMACEN"] = <GuideInWarehouse />
opciones["TRANSITO"] = <GuideInTransit />
opciones["ENTREGADAS"] = <GuideDelivered />
opciones["SEGUIMIENTO"] = <GuideTracking />
opciones["DEVUELTACLIENTE"] = <GuideReturnToCustomer />
opciones["DEVUELTAS"] = <GuideReturned />

const Guides = () => {
    const [option, setOption] = useState("ENTRADA");

    const handleOption = (event) => {
        setOption(event.target.name)
    };

    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>GUIAS DE ENTREGA</Tittle>
                </Box>
                <NavContainer>
                    <NavApp>
                        {
                            ordersOptions.map(
                                oP => (
                                    <NavElement 
                                        key={oP.id} 
                                        name={oP.value} 
                                        isActive={oP.value === option ? true : false}
                                        onClick={handleOption}
                                    >
                                        {oP.tittle}
                                    </NavElement>
                                )
                            )
                        }
                    </NavApp>
                </NavContainer>
                {opciones[option]}
            </Container>
        </Layout>
    )
};

export default Guides;