import dayjs from 'dayjs';
import styled from 'styled-components';
import { useRef, useState, useEffect } from 'react';
import { Modal, Form } from 'react-bootstrap';
import toast, { Toaster } from 'react-hot-toast';

import config from '../config';

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const NewOrderModal = ({ show, handleClose, setOrders, orders }) => {
    const fechaRef = useRef(null);
    const almacenRef = useRef(null);
    const clienteRef = useRef(null);
    const sucursalRef = useRef(null);
    const paquetesRef = useRef(null);
    const [loading, setLoading] = useState(true);
    const [message, setMessage] = useState("");
    const [almacenes, setAlmacenes] = useState([]);
    const [clientes, setClientes] = useState([]);
    const [clienteActivo, setClienteActivo] = useState("");
    const [sucursales, setSucursales] = useState([]);
    const [sucursalActiva, setSucursalActiva] = useState("");

    console.log(loading)

    useEffect(()=> {
        async function fetchData(){
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            let warehouses = await responseW.json();
            setAlmacenes(warehouses);
            let responseC = await fetch(`${config.urlBase}clientes/`);
            let customers = await responseC.json();
            setClientes(customers);
            setClienteActivo(customers[0]);
            let responseB = await fetch(`${config.urlBase}sucursales/`);
            let branches = await responseB.json();
            setSucursales(branches.data);
            setLoading(false);
            let branchesByClient = branches.data.filter(b => b.cliente == customers[0].cliente)
            setSucursalActiva(branchesByClient[0]);
        }
        fetchData()
    },[]);

    const handleOrder = async () => {
        try {
        let chofer = null;
        if (paquetesRef.current.value.length < 1) {
            throw Error('Se debe especificar la cantidad de paquetes')
        }
        //TODO: Agregar el usuario de la sesion
        let response = await fetch(`${config.urlBase}ordenes/`,{
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json'
              },
            body: JSON.stringify({
                orden: 0,
                fecha: fechaRef.current.value,
                almacen: almacenRef.current.value,
                cliente: clienteRef.current.value,
                sucursal: sucursalRef.current.value,
                paquetes: paquetesRef.current.value,
                usuarioCaptura: "ERETIZ",
                estatus: "Nueva",
                choferRecoleccion: chofer
            })
        })
        let res = await response.json()
        if (res.type === "Error") {
            throw Error(res.message);
        }
        setOrders(orders.concat({
            orden: res.data,
            fecha: fechaRef.current.value,
            almacen: almacenRef.current.value,
            cliente: clienteRef.current.value,
            sucursal: sucursalRef.current.value,
            paquetes: paquetesRef.current.value,
            usuarioCaptura: "ERETIZ",
            estatus: "Nueva",
            choferRecoleccion: chofer
        }));
        setMessage(res.message);
        toast.success(res.message)
        handleClose();   
        } catch (error) {
            toast.error(error.message)
        }        
    }

    const handleChangeClient = () => {
        let customerFiltered = clientes.filter(c => c.cliente === parseInt(clienteRef.current.value,10));
        setClienteActivo(customerFiltered[0]);
    }

    const handleChangeBranch = (event) => {
        let branchFiltered = sucursales.filter(sucursal => sucursal.sucursal == event.target.value)
        setSucursalActiva(branchFiltered[0]);
    }

    return (
        <Modal show={show} onHide={handleClose} backdrop="static">
        <Modal.Header closeButton>
        <Modal.Title>NUEVA ORDEN DE RECOLECCION</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Group className="mb-3" >
                    <Form.Label>Fecha</Form.Label>
                    <Form.Control type="datetime-local" ref={fechaRef} onChange={() => console.log(dayjs.utc().local().format('YYYY-MM-DDThh:mm:ss a'))} value={dayjs.utc().local().format('YYYY-MM-DDTHH:mm')}/>
                </Form.Group>
                <Form.Group className="mb-3" >
                    <Form.Label>Cliente</Form.Label>
                    <Form.Select aria-label="Default select example" ref={clienteRef} onChange={handleChangeClient}>
                        {clientes.map(cliente => <option key={cliente.cliente} value={cliente.cliente}>{cliente.razonSocial}</option>)}
                    </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" >
                    <Form.Label>Origen</Form.Label>
                    <Form.Select aria-label="Default select example" ref={sucursalRef} onChange={e => handleChangeBranch(e)}>
                        {sucursales.map(sucursal => {
                            if(sucursal.cliente == clienteActivo.cliente){
                                return <option key={sucursal.sucursal} value={sucursal.sucursal}>{sucursal.descripcion}</option>
                            }
                            return <></>
                            })}
                    </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" >
                    <Form.Label>Destino</Form.Label>
                    <Form.Select aria-label="Default select example" ref={almacenRef}>
                        {
                            almacenes.map(almacen => {
                                if(almacen.almacen == sucursalActiva.almacen) {
                                    return <option selected key={almacen.almacen} value={almacen.almacen}>{almacen.descripcion}</option>
                                } else {
                                    return <option key={almacen.almacen} value={almacen.almacen}>{almacen.descripcion}</option>;   
                                }
                            })
                        }
                    </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" >
                    <Form.Label>Paquetes</Form.Label>
                    <Form.Control type="number" ref={paquetesRef}/>
                </Form.Group>
            </Form>
        </Modal.Body>
        <Modal.Footer>
        <CustomButtonOutline onClick={handleClose}>
            CANCELAR
        </CustomButtonOutline>
        <CustomButton onClick={handleOrder}>
            AGREGAR
        </CustomButton>
        </Modal.Footer>
        <Toaster />
    </Modal>
    )
};

export default NewOrderModal;