import { useRef, useState, useEffect } from "react";
import { InputGroup, FormControl, Button, Row, Col, Table, Modal } from "react-bootstrap";
import toast, { Toaster } from 'react-hot-toast';
import CropFreeIcon from '@material-ui/icons/CropFree';
import CheckIcon from '@material-ui/icons/Check';

import config from '../config';

const GuideReturnRegistration = () => {
    let guide;
    const [guidesReturn, setGuidesReturn] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const guiaRef = useRef(null);
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const handleEnter = async (event) => {
        if(event.key === "Enter") {
            if(guiaRef.current.value === "") return;
            guide = await fetch(`${config.urlBase}guias/consulta/guia?guia=${guiaRef.current.value}`);
            guide = await guide.json();
            guide = guide.data;
            if(!guide || guide.estatus !== 'Devuelta a Chofer') {
                toast.error('Guia no marcada como devolucion');
            } else {
                handleShow()
            }
        }
    };

    const handleReturnToWarehouse = async () => {
        guide = await fetch(`${config.urlBase}guias/consulta/guia?guia=${guiaRef.current.value}`);
        guide = await guide.json();
        guide = guide.data;
        let responseReturn = await fetch(`${config.urlBase}guias/almacen-devolucion`,{
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json'
              },
              body: JSON.stringify({
                  orden: guide.orden,
                  guia: guide.guia
              })
        });
        responseReturn = await responseReturn.json();
        setGuidesReturn(guidesReturn.filter(g => g.guia !== guiaRef.current.value))
        guiaRef.current.value = "";
        toast.success(responseReturn.message);
        setShow(false)
    }

    const handleReturnToCustomer = async () => {
        guide = await fetch(`${config.urlBase}guias/consulta/guia?guia=${guiaRef.current.value}`);
        guide = await guide.json();
        guide = guide.data;
        let responseReturn = await fetch(`${config.urlBase}guias/almacen-devolucion-directa`,{
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json'
              },
              body: JSON.stringify({
                  guia: guide.guia
              })
        });
        responseReturn = await responseReturn.json();
        setGuidesReturn(guidesReturn.filter(g => g.guia !== guiaRef.current.value))
        guiaRef.current.value = "";
        toast.success(responseReturn.message);
        setShow(false)
    }

    const handleClick = async () => {
        if(guiaRef.current.value === "") return
        guide = await fetch(`${config.urlBase}guias/consulta/guia?guia=${guiaRef.current.value}`);
        guide = await guide.json();
        guide = guide.data;
        if(!guide || guide.estatus !== 'Devuelta a Chofer') {
            toast.error('Guia no marcada como devolucion');
        } else {
            handleShow()
        }
    };

    useEffect(() => {
        async function fetchData() {
            let responseG = await fetch(`${config.urlBase}guias/consulta/guias/devolucion-cliente`);
            responseG = await responseG.json();
            setGuidesReturn(responseG.data);
            let choferes = await fetch(`${config.urlBase}choferes/`);
            choferes = await choferes.json();
            setDrivers(choferes);
        }
        fetchData();
    },[])

    return (
        <>
        <InputGroup className="mb-3">
            <InputGroup.Text id="basic-addon1"># Guia</InputGroup.Text>
            <FormControl
                aria-label="Guia"
                autoFocus
                ref={guiaRef}
                onKeyUp={handleEnter}
            />
            <Button variant="outline-secondary" id="button-addon2" className="d-sm-none">
                <CropFreeIcon /> 
                <small>ESCANEAR</small>
            </Button>
            <Button variant="outline-secondary" id="button-addon2" onClick={handleClick} className="d-none d-sm-block">
                <CheckIcon /> 
                <small>REGISTRAR</small>
            </Button>
        </InputGroup>
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>DEVOLUCION A CLIENTE</Modal.Title>
            </Modal.Header>
            <Modal.Body>Registrar guia como devolucion directa a cliente? <br />Al seleccionar NO la guia se registrara como devuelta a almacen.</Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleReturnToWarehouse}>
                    NO
                </Button>
                <Button variant="primary" onClick={handleReturnToCustomer}>
                    SI
                </Button>
            </Modal.Footer>
        </Modal>
        <Row>
            <Col> 
                {guidesReturn.length >0 && 
                    <Table responsive striped bordered>
                        <thead>
                            <tr>
                                <th>guia</th>
                                <th>chofer</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                guidesReturn.map(g => (
                                    <tr key={g.guia}>
                                        <td>
                                            {g.guia}
                                        </td>
                                        <td>
                                            {drivers.map(d => d.chofer === g.choferEntrega ? d.nombre : "")}
                                        </td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </Table>
                    }
            </Col>
        </Row>
      <Toaster />
      </>
    )
};

export default GuideReturnRegistration;