import { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@material-ui/icons/Delete';
import { Table, Modal,InputGroup, FormControl, Form } from 'react-bootstrap';

import config from '../config';

const TableContainer = styled.div`
    margin: auto;
    width: 780px;
    height: 300px;
    overflow-y: scroll;
    @media (max-width: 860px) {
        width: 780px;
    }
    @media (max-width: 760px) {
        width: 580px;
    }
    @media (max-width: 560px) {
        width: 380px;
    }
    @media (max-width: 360px) {
        width: 280px;
    }
`;

const DeleteButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: red;
    }
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CatalogZip = () => {
    const [areas, setAreas] = useState([]);
    const [elements, setElements] = useState([]);
    const [selectElement, setSelectElement] = useState({});
    const [showAddModal, setShowAddModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const codigoAgregarRef = useRef(null);
    const descripcionAgregarRef = useRef(null);

    useEffect(()=> {
        async function fetchData(){
            let responseA = await fetch(`${config.urlBase}areas/`);
            let areas = await responseA.json();
            setAreas(areas);
            let responseB = await fetch(`${config.urlBase}areasCP/`);
            let zips = await responseB.json();
            setElements(zips);
        }
        fetchData()
    },[]);

    const handleCloseAddModal = () => setShowAddModal(false);
    const handleShowAddModal = () => setShowAddModal(true);
    const handleCloseDeleteModal = () => setShowDeleteModal(false);
    const handleShowDeleteModal = (element) => {
        setSelectElement(element);
        setShowDeleteModal(true);
    };
    

    const handleDelete = async () => {
        let response = await fetch(`${config.urlBase}areasCP/eliminar?area=${selectElement.area}&codigoPostal=${selectElement.codigoPostal}`,{
            method: 'DELETE'
        });
        let data = await response.json();
        console.log(data);
        setElements(elements.filter(element => element.codigoPostal !== selectElement.codigoPostal));
        setShowDeleteModal(false);
    };

    const handleAdd = async () => {
        let newElement = {};
        newElement.area = descripcionAgregarRef.current.value;
        newElement.codigoPostal = codigoAgregarRef.current.value;
        let response = await fetch(`${config.urlBase}areasCP/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        setElements(elements.concat(response.data))
        setShowAddModal(false);
    };

    return (
        <>
            <CustomButton onClick={handleShowAddModal}>AGREGAR</CustomButton>
            <Modal
                show={showAddModal}
                onHide={handleCloseAddModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>AGREGAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Area</InputGroup.Text>
                        <Form.Select aria-label="Default select example" ref={descripcionAgregarRef} >
                            {
                               areas.map(area => <option key={area.area} value={area.area}>{area.descripcion}</option>)
                            }
                        </Form.Select>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text 
                            id="inputGroup-sizing-sm">Codigo Postal</InputGroup.Text>
                        <FormControl 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            ref={codigoAgregarRef} 
                            maxLength="10"
                        />
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleAdd} >AGREGAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseAddModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showDeleteModal}
                onHide={handleCloseDeleteModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>CONFIRMACION NECESARIA!</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {`Al precionar aceptar, el registro ${selectElement.area} ser elminado permanentemente, desea continuar?`}
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleDelete} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseDeleteModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <TableContainer>
                <h3>CODIGOS POSTALES</h3>
                <Table striped hover responsive>
                    <thead>
                        <tr>
                        <th>Codigo Postal</th>
                        <th>Area</th>
                        <th>{" "}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            elements.length > 0 && elements.map( zip => (
                                <tr key={zip.codigoPostal}>
                                    <td>
                                        {zip.codigoPostal}
                                    </td>
                                    <td>{areas.map(area => zip.area === area.area ? area.descripcion : "")}</td>
                                    <td>
                                        <DeleteButton onClick={()=>handleShowDeleteModal(zip)}>
                                            <DeleteIcon />
                                        </DeleteButton>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </Table>
                {
                    elements.length === 0 && <h1>No hay datos por mostrar</h1>
                }
            </TableContainer>
        </>
    )
};

export default CatalogZip;