import { useState,useEffect,useRef,useReducer,useContext } from "react";
import dayjs from "dayjs";
import { Row, Col, FloatingLabel, Form, Table } from "react-bootstrap";
import styled from "styled-components";
import toast, { Toaster } from 'react-hot-toast';
import AppContext from "../../context/AppContext";

import config from "../../config";

//Config objects
const headers = [
    {
        id: 1,
        name: "Cliente"
    },
    {
        id: 2,
        name: "Sucursal"
    },
    {
        id: 3,
        name: "Gua"
    },
    {
        id: 4,
        name: "Fecha"
    },
];

const initialState = {
    markedGuides: 0,
    selectedGuides: []
};

//Reducers
function reducer(state, action) {
    switch (action.type) {
        case "add_one":
            return {
                markedGuides: state.markedGuides+1,
                selectedGuides: state.selectedGuides.concat(action.payload)
            }
        case "substract_one":
            return {
                markedGuides: state.markedGuides-1,
                selectedGuides: state.selectedGuides.filter(selectedGuide => selectedGuide.guia !== action.payload.guia)
            }
        case "add_all":
            return {
                markedGuides: action.payload.length,
                selectedGuides: action.payload
            }
        case "substract_all":
            return {
                markedGuides: 0,
                selectedGuides: []
            }
        default:
            throw new Error();
    }
}

//Styled components
const TableContainer = styled.div`
    width: 100%;
    height: 400px;
    overflow-y: scroll;
`;

const Display = styled.div`
    position: fixed;
    width: 45%;
    padding: 10px;
    border: solid 1.5px black;
    height: 510px;
`;

const GuideContainer = styled.div`
    width: 100%;
    height: 250px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    position: absolute;
    bottom: 20px;
    right: 20px;
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
`;

//Component
const ToCollect = () => {
    let utc = require("dayjs/plugin/utc");
    dayjs.extend(utc);

    //Context
    const { state: { user } } = useContext(AppContext);

    //Reducer
    const [state, dispach] = useReducer(reducer,initialState);

    //States
    const [customers, setCustomers] = useState([]);
    const [branches, setBranches] = useState([]);
    const [guides, setGuides] = useState([]);
    const [filteredGuides, setFilteredGuides] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const [warehouses, setWarehouses] = useState([]);

    //Refs
    const branchRef = useRef(null);
    const driverRef = useRef(null);
    const warehouseRef = useRef(null);
    const allCheckboxRef = useRef(null);

    //Effects
    useEffect(() => {
        async function fetchData() {
            //Fetch customers
            let responseCustomers = await fetch(`${config.urlBase}clientes/`);
            responseCustomers = await responseCustomers.json();
            setCustomers(responseCustomers);
            //Fetch branches
            let responseBranches = await fetch(`${config.urlBase}sucursales/`);
            responseBranches = await responseBranches.json();
            setBranches(responseBranches.data);
            //Fetch guides
            let responseGuides = await fetch(`${config.urlBase}guias/consulta/guias/por-recolectar`);
            responseGuides = await responseGuides.json();
            setGuides(responseGuides.data);
            setFilteredGuides(responseGuides.data);
            //Fetch drivers
            let responseDrivers = await fetch(`${config.urlBase}choferes/`);
            responseDrivers = await responseDrivers.json();
            setDrivers(responseDrivers);
            //Fetch warehouses
            let responseWarehouses = await fetch(`${config.urlBase}almacenes/`);
            responseWarehouses = await responseWarehouses.json();
            setWarehouses(responseWarehouses);
        }
        fetchData();
    },[]);

    //Functions
    const handleChange = (e) => {
        let name = e.target.name;
        let value = e.target.value;

        switch (name) {
            case "branch":
                dispach({type: "substract_all"});
                allCheckboxRef.current.checked = false;
                if (value === "all") {
                    setFilteredGuides(guides);    
                } else {
                    let filtered = guides.filter(guide => guide.sucursal === value);
                    setFilteredGuides(filtered);
                }
                break;
            case "driver":
                
                break;
            case "warehouse":
                
                break;
            default:
                break;
        }

    }

    const handleCheck = (e, guide) => {
        if (e.target.checked) {
            let filtered = guides.filter(guideReference => guideReference.sucursal === guide.sucursal);
            setFilteredGuides(filtered);
            branchRef.current.value = guide.sucursal;
            let warehouse = branches.find(branch => branch.sucursal === guide.sucursal);
            warehouseRef.current.value = warehouse.almacen;
            dispach({type: "add_one", payload: guide});
            if (state.selectedGuides.length+1 === filteredGuides.length) {
                allCheckboxRef.current.checked = true;
            }
        } else {
            dispach({type: "substract_one", payload: guide});
            if (state.selectedGuides.length-1 < filteredGuides.length) {
                allCheckboxRef.current.checked = false;
            }
            /*if (state.selectedGuides.length-1 === 0) {
                branchRef.current.value = "all";
                setFilteredGuides(guides);
            }*/
        }

    }

    const handleAllCheck = (e) => {
        if (e.target.checked) {
            dispach({type: "add_all", payload: filteredGuides})
        } else {
            dispach({type: "substract_all"})
        }
    }

    const handleClick = (e, guide) => {
        dispach({type: "substract_one", payload: guide});
        if (state.selectedGuides.length-1 < filteredGuides.length) {
            allCheckboxRef.current.checked = false;
        }
    }

    const handleNewOrder = async () => {
        try {
            if (state.selectedGuides.length === 0) {
                throw new Error("No hay guas seleccionadas");
            }
            let response = await fetch(`${config.urlBase}ordenes/`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    orden: 0,
                    fecha: dayjs().utc().format("YYYY-MM-DD HH:mm:ss"),
                    almacen: warehouseRef.current.value,
                    cliente: state.selectedGuides[0].cliente,
                    sucursal: branchRef.current.value,
                    paquetes: state.selectedGuides.length,
                    usuarioCaptura: user.usuario,
                    estatus: "Asignada",
                    choferRecoleccion: driverRef.current.value,
                    guias: state.selectedGuides
                })
            });
            response = await response.json();
            if (response.type === "Success") {
                toast.success("Orden creada con xito");
                dispach({type: "substract_all"});
                allCheckboxRef.current.checked = false;
                branchRef.current.value = "all";
                let responseGuides = await fetch(`${config.urlBase}guias/consulta/guias/por-recolectar`);
                responseGuides = await responseGuides.json();
                setGuides(responseGuides.data);
                setFilteredGuides(responseGuides.data);
            }
        } catch (error) {
            console.log(error);
            toast.error("Error al crear la orden, intente de nuevo");
        } 
    }

    const mapingBranch = (guide) => {
        let branch = branches.find(branch => branch.sucursal === guide.sucursal);
        return branch.descripcion;
    }

    const mapingCustomer = (guide) => {
        let customer = customers.find(customer => customer.cliente === guide.cliente);
        return customer.nombreCorto;
    }

    return (
        <>
            <Row>
                <Col xs={6}>
                    <FloatingLabel controlId="floatingSelect" label="Sucursal" className="mb-4 mt-4">
                        <Form.Select aria-label="Floating label select example" ref={branchRef} onChange={handleChange} name="branch">
                            <option defaultValue key="default" value="all">Todas</option>
                            {
                                branches.map(branch => <option key={branch.sucursal} value={branch.sucursal}>{branch.descripcion}</option>)
                            }
                        </Form.Select>
                    </FloatingLabel>
                </Col>
                <Col>
                    <Display>
                        <Row>
                            <Col>
                                <h6>NUEVA ORDEN DE RECOLECCION</h6> 
                            </Col>
                            <Col>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <FloatingLabel controlId="floatingSelect" label="Chofer" className="mb-4 mt-4">
                                    <Form.Select aria-label="Floating label select example" ref={driverRef} onChange={handleChange} name="driver">
                                        {
                                            drivers.map(driver => <option key={driver.chofer} value={driver.chofer}>{driver.nombre}</option>)
                                        }
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                            <Col>
                                <FloatingLabel controlId="floatingSelect" label="Destino" className="mb-4 mt-4">
                                    <Form.Select aria-label="Floating label select example" ref={warehouseRef} onChange={handleChange} name="warehouse">
                                        {
                                            warehouses.map(warehouse => <option key={warehouse.almacen} value={warehouse.almacen}>{warehouse.descripcion}</option>)
                                        }
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                        </Row>
                        <h6>TOTAL DE GUIAS ASIGNADAS A LA ORDEN: {state.selectedGuides.length}</h6>
                        <GuideContainer>
                            <Table hover responsive bordered>
                                <tbody>
                                    {
                                        state.selectedGuides.map(guide => <tr key={guide.guia} onClick={(e) => handleClick(e,guide)}>
                                            <td>{mapingCustomer(guide)}</td>
                                            <td>{mapingBranch(guide)}</td>
                                            <td>{guide.guia}</td>
                                            <td>{dayjs.utc(guide.fechaEnvio).format('DD/MM/YYYY')}</td>
                                        </tr>)
                                    }
                                </tbody>
                            </Table>
                        </GuideContainer>
                        <CustomButton onClick={handleNewOrder}>
                            AGREGAR
                        </CustomButton> 
                    </Display>
                </Col>
            </Row>
            <Row>
                <Col>
                    <TableContainer>
                        <Table hover responsive striped bordered>
                            <thead>
                                <tr>
                                    <th>
                                        <input type="checkbox" onChange={(event) => handleAllCheck(event)} ref={allCheckboxRef}/>
                                        {state.markedGuides > 0 && <small style={{marginLeft:"5px"}}><strong> {state.markedGuides}</strong></small>}
                                    </th>
                                    {
                                        headers.map(header => <th key={header.id}>{header.name}</th>)
                                    }
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    filteredGuides.map(guide => <tr key={guide.guia}>
                                        <td>
                                            <input type="checkbox" checked={state.selectedGuides.includes(guide)} onChange={(event) => handleCheck(event, guide)}/>
                                        </td>
                                        <td>{mapingCustomer(guide)}</td>
                                        <td>{mapingBranch(guide)}</td>
                                        <td>{guide.guia}</td>
                                        <td>{dayjs.utc(guide.fechaEnvio).format('DD/MM/YYYY')}</td>
                                    </tr>)
                                }
                            </tbody>
                        </Table>
                    </TableContainer>
                </Col>
                <Col>
                </Col>
            </Row>
            <Toaster />
        </>
    )
}

export default ToCollect;