import { useState } from 'react';
import styled from "styled-components";
import Layout from "../components/Layout";
import ReportControl from '../components/ReportControl';
import ReportReturns from '../components/ReportReturns';
import ReportTimes from '../components/ReportTimes';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const NavContainer = styled.div`
    width: 90vw;

    overflow-x: scroll;
    ::-webkit-scrollbar {display: none;}
`;

const NavApp = styled.div`
    display: flex;
    align-items: center;
    justify-contetn: space-around;
`;

const NavElement = styled.button`
    background: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-weight: ${props => props.isActive ? "bold;" : "initial;"}

    &:hover {
        font-weight: bold;
    }
`;

const ordersOptions = [
    {id:1,tittle:'DEVOLUCION A TIENDA',value:'DEVOLUCION'}, 
    {id:2,tittle:'TIEMPOS',value:'TIEMPOS'},
    {id:3,tittle:'CONTROL DE SERVICIOS',value:'CONTROL'}
]

const opciones = {};
opciones["DEVOLUCION"] = <ReportReturns />
opciones["TIEMPOS"] = <ReportTimes />
opciones["CONTROL"] = <ReportControl />

const Reports = () => {
    const [option, setOption] = useState("DEVOLUCION");

    const handleOption = (event) => {
        setOption(event.target.name)
    };
    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>ORDENES DE RECOLECCION</Tittle>
                </Box>
                <NavContainer>
                    <NavApp>
                        {
                            ordersOptions.map(
                                orderOption => (
                                    <NavElement 
                                        key={orderOption.id} 
                                        name={orderOption.value} 
                                        isActive={orderOption.value === option ? true : false}
                                        onClick={handleOption}
                                    >
                                        {orderOption.tittle}
                                    </NavElement>
                                )
                            )
                        }
                    </NavApp>
                </NavContainer>
                {opciones[option]}
            </Container>
        </Layout>
    )
}

export default Reports;