import toast, { Toaster } from 'react-hot-toast';
import styled from 'styled-components';
import { useState, useEffect, useRef } from 'react';
import { InputGroup, FormControl, Row, Col, Alert } from 'react-bootstrap';

import config from '../config';

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CatalogParameters = () => {
    const [parametros, setParametros] = useState({});
    const limiteRef = useRef(null);

    useEffect(()=> {
        async function fetchData(){
            let response = await fetch(`${config.urlBase}parameters/`);
            response = await response.json();
            setParametros(response)
        }
        fetchData()
    },[]);

    const handleChange = async (event) => {
        setParametros({
            ...parametros,
            [event.target.name]:limiteRef.current.value
        })
        if(event.target.name === 'CantidadDevoluciones'){
            let response = await fetch(`${config.urlBase}parameters/update-limit`,{
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    limite: limiteRef.current.value
                })
            });
            response = await response.json();
            toast.success(response.message);
        }
    };

    return (
        <>
            <Row>
                <Col>
                <h1 className="mt-5">PARAMETROS</h1>
                <InputGroup className="mb-3 mt-5">
                    <InputGroup.Text id="basic-addon3">
                        Limite de devoluciones
                    </InputGroup.Text>
                    <FormControl 
                        type="number" 
                        onChange={e => handleChange(e)} 
                        ref={limiteRef} 
                        value={parametros.CantidadDevoluciones}
                        name="CantidadDevoluciones"
                    />
                </InputGroup>
                </Col>
                <Col>
                </Col>
                <Col>
                </Col>
            </Row>
            <Toaster/>
        </>
    )
};

export default CatalogParameters;