import { useState,useEffect,useRef,useReducer } from "react";
import { Row, Col, FloatingLabel, Form, Table, Modal, Button, Card, List, ListGroup } from "react-bootstrap";
import toast, { Toaster } from 'react-hot-toast';
import styled from "styled-components";
import dayjs from "dayjs";

import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/AddBoxSharp'
import RemoveIcon from '@material-ui/icons/RemoveCircle'

import config from "../../config";

//Config objects
const initialState = {
    markedOrders: 0,
    selectedOrders: []
};

//Reducers
function reducer(state, action) {
    switch (action.type) {
        case "add_one":
            return {
                markedOrders: state.markedOrders+1,
                selectedOrders: state.selectedOrders.concat(action.payload)
            }
        case "substract_one":
            return {
                markedOrders: state.markedOrders-1,
                selectedOrders: state.selectedOrders.filter(selectedOrder => selectedOrder.orden !== action.payload.orden)
            }
        case "add_all":
            return {
                markedOrders: action.payload.length,
                selectedOrders: action.payload
            }
        case "substract_all":
            return {
                markedOrders: 0,
                selectedOrders: []
            }
        default:
            throw new Error();
    }
}

//Styled components
const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    margin-bottom: 20px;
`;

const AddGuideButton = styled.button`
    color: #6ECB63;
    background-color: transparent;
    border: none;
    margin: 0;
`;

const RemoveGuideButton = styled.button`
    background-color: transparent;
    border: none;
    margin: 0;
    color: #FF5A5A;
`;

const TableContainer = styled.div`
    width: 100%;
    height: 300px;
    overflow-y: scroll;
`;

const ListContainer = styled.div`
    width: 100%;
    height: 300px;
    overflow-y: scroll;
`;

const IconContainer = styled.button`
    border: none;
    background-color: transparent;
    width: 100%;
    height: 100%;
    color: #000;
    cursor: pointer;
`

//Component
const CollectOrders = () => {
    //Reducer
    const [state, dispach] = useReducer(reducer,initialState);

    //Refs
    const warehouseRef = useRef(null);
    const allCheckboxRef = useRef(null);

    //State
    const [customers, setCustomers] = useState([]);
    const [branches, setBranches] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const [orders, setOrders] = useState([]);
    const [selectedOrder, setSelectedOrder] = useState({});
    const [guidesOfSelectedOrder, setGuidesOfSelectedOrder] = useState([]);
    const [warehouses, setWarehouses] = useState([]);
    const [filteredOrders, setFilteredOrders] = useState([]);
    const [pendingGuidesToCollect, setPendingGuidesToCollect] = useState([]);
    const [show, setShow] = useState(false);
    const [showEdit, setShowEdit] = useState(false);
    const [showAddGuide, setShowAddGuide] = useState(false);

    //Effects
    useEffect(() => {
        async function fetchData() {
            //Fetch customers
            let responseCustomers = await fetch(`${config.urlBase}clientes/`);
            responseCustomers = await responseCustomers.json();
            setCustomers(responseCustomers);
            //Fetch branches
            let responseBranches = await fetch(`${config.urlBase}sucursales/`);
            responseBranches = await responseBranches.json();
            setBranches(responseBranches.data);
            //Fetch drivers
            let responseDrivers = await fetch(`${config.urlBase}choferes/`);
            responseDrivers = await responseDrivers.json();
            setDrivers(responseDrivers);
            //Fetch warehouses
            let responseWarehouses = await fetch(`${config.urlBase}almacenes/`);
            responseWarehouses = await responseWarehouses.json();
            setWarehouses(responseWarehouses);
            //Fetch orders
            let responseOrders = await fetch(`${config.urlBase}ordenes/asignadas/all/`);
            responseOrders = await responseOrders.json();
            setOrders(responseOrders.data);
            setFilteredOrders(responseOrders.data);
        }
        fetchData();
    }, [])

    //Functions
    const handleClose = () => setShow(false);

    const handleShow = () => {
        if (state.selectedOrders.length === 0) {
            toast.error("No se ha seleccionado ninguna orden");
            return;
        }
        setShow(true)
    };

    const handleCloseEdit = () => setShowEdit(false);

    const handleShowEdit = async (order) => {
        try {
            let response = await fetch(`${config.urlBase}guias/${order.orden}/`);
            response = await response.json();
            let orderToBeEdited = {
                ...order,
                guias: response
            }
            setSelectedOrder(orderToBeEdited);
            setGuidesOfSelectedOrder(response);
            setShowEdit(true);
        } catch (error) {
            console.log(error);
            toast.error("Error al obtener la informacin de la orden");
        }
    };

    const handleCloseAddGuide = () => setShowAddGuide(false);

    const handleShowAddGuide = async () => {
        try {
            let response = await fetch(`${config.urlBase}guias/consulta/guias/por-sucursal?sucursal=${selectedOrder.sucursal}`);
            response = await response.json();
            if (response.type === "Error") {
                toast.error("No hay guas disponibles para la sucursal seleccionada");
                return;
            }
            setPendingGuidesToCollect(response.data);
            setShowAddGuide(true)
        } catch (error) {
            console.log(error);
            toast.error(error.message);
        }
    };

    const handleChange = (e) => {
        let value = e.target.value;
        let name = e.target.name;
        switch(name) {
            case "warehouse":
                if (value === "all") {
                    setFilteredOrders(orders);
                } else {
                    setFilteredOrders(orders.filter(order => order.almacen.toLowerCase().includes(value.toLowerCase())));
                }
                dispach({type: "substract_all"});
                allCheckboxRef.current.checked = false;
                break;
            default:
                break;
        }
    };

    const handleCheck = (e, order) => {
        if (e.target.checked) {
            dispach({type: "add_one", payload: order});
            if (state.selectedOrders.length+1 === filteredOrders.length) {
                allCheckboxRef.current.checked = true;
            }
        } else {
            dispach({type: "substract_one", payload: order});
            if (state.selectedOrders.length-1 < filteredOrders.length) {
                allCheckboxRef.current.checked = false;
            }
        }
    };

    const handleAllCheck = (e) => {
        if (e.target.checked) {
            dispach({type: "add_all", payload: filteredOrders});
        } else {
            dispach({type: "substract_all", payload: filteredOrders});
        }
    };

    const handleDelete = async () => {
        try {
            let response = await fetch(`${config.urlBase}ordenes/eliminar`, {
                method: "DELETE",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    ordenes: state.selectedOrders
                })
            });
            response = await response.json();
            if (response.type === "Success") {
                setShow(false);
                dispach({type: "substract_all", payload: state.selectedOrders});
                let responseOrders = await fetch(`${config.urlBase}ordenes/asignadas/all/`);
                responseOrders = await responseOrders.json();
                setOrders(responseOrders.data);
                setFilteredOrders(responseOrders.data);
                toast.success("Ordenes eliminadas");
            } else {
                throw new Error("Error al eliminar las ordenes");
            }
        } catch (error) {
            console.error(error);
            toast.error(error.message);
        }
    };

    const handleDeleteGuide = async (guide) => {
        try {
            let response = await fetch(`${config.urlBase}guias/`, {
                method: "DELETE",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    guia: guide.guia
                })
            });
            response = await response.json();
            if (response.type === "Succes") {
                let responseOrders = await fetch(`${config.urlBase}ordenes/asignadas/all/`);
                responseOrders = await responseOrders.json();
                setGuidesOfSelectedOrder(guidesOfSelectedOrder.filter(guideS => guideS.guia !== guide.guia));
                setOrders(responseOrders.data);
                setFilteredOrders(responseOrders.data);
                toast.success("Gua removida");
            } else {
                throw new Error("Error al remover la gua");
            }
        } catch (error) {
            console.error(error);
            toast.error(error.message);
        }
    };

    const handleAddGuide = async (guide) => {
        try {
            let response = await fetch(`${config.urlBase}guias/asignar-guia-a-orden/`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    guia: guide.guia,
                    orden: selectedOrder
                })
            });
            response = await response.json();
            if (response.type === "Success") {
                let responseOrders = await fetch(`${config.urlBase}ordenes/asignadas/all/`);
                responseOrders = await responseOrders.json();
                setPendingGuidesToCollect(pendingGuidesToCollect.filter(guideS => guideS.guia !== guide.guia));
                setGuidesOfSelectedOrder([...guidesOfSelectedOrder, guide]);
                setOrders(responseOrders.data);
                setFilteredOrders(responseOrders.data);
                toast.success("Gua agregada");
            } else {
                throw new Error("Error al agregar la gua");
            }
        } catch (error) {
            console.error(error);
            toast.error(error.message);
        }
    };

    const mapingBranch = (order) => {
        let branch = branches.find(branch => branch.sucursal === order.sucursal);
        if (branch) {
            return branch.descripcion;
        } else {
            return "";
        }
    };

    const mapingCustomer = (order) => {
        let customer = customers.find(customer => customer.cliente === order.cliente);
        if (customer) {
            return customer.nombreCorto;
        } else {
            return "";
        }
    };

    const mapingDriver = (order) => {
        let driver = drivers.find(driver => driver.chofer === order.choferRecoleccion);
        if (driver) {
            return driver.nombre;
        } else {
            return "";
        }
    };

    const mapingWarehouse = (order) => {
        let warehouse = warehouses.find(warehouse => warehouse.almacen === order.almacen);
        if (warehouse) {
            return warehouse.descripcion;
        } else {
            return "";
        }
    };

    return (
        <>
            <Row>
                <Col xs={6}>
                    <FloatingLabel controlId="floatingSelect" label="Almacen" className="mb-4 mt-4">
                        <Form.Select aria-label="Floating label select example" ref={warehouseRef} onChange={handleChange} name="warehouse">
                            <option defaultValue key="default" value="all">Todas</option>
                            {
                                warehouses.map(warehouse => <option key={warehouse.almacen} value={warehouse.almacen}>{warehouse.descripcion}</option>)
                            }
                        </Form.Select>
                    </FloatingLabel>
                </Col>
            </Row>
            <Row>
                <Col>
                    <CustomButton onClick={handleShow}>
                        ELIMINAR ORDENES SELECCIONADAS
                    </CustomButton>
                    <Modal
                        show={show}
                        onHide={handleClose}
                        backdrop="static"
                        keyboard={false}
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>Eliminar Ordenes</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <p>Est seguro que desea eliminar las ordenes seleccionadas?</p>
                            <p>Ordenes seleccionadas:</p>
                            <Table bordered responsive>
                                <thead>
                                    <tr>
                                        <th>Orden</th>
                                        <th>Paquetes</th>
                                        <th>Chofer</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        state.selectedOrders.map(order => (
                                            <tr key={order.orden}>
                                                <td>{order.orden}</td>
                                                <td>{order.paquetes}</td>
                                                <td>{mapingDriver(order)}</td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </Table>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={handleClose}>
                                CANCELAR
                            </Button>
                            <Button variant="warning" onClick={handleDelete}>
                                <strong>ELMIMINAR</strong>
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </Col>
            </Row>
            <Row>
                <Col>
                    <TableContainer>
                        <Table hover responsive bordered striped>
                            <thead>
                                <tr>
                                    <th>
                                        <input type="checkbox" onChange={(event) => handleAllCheck(event)} ref={allCheckboxRef}/>
                                        {state.markedOrders > 0 && <small style={{marginLeft:"5px"}}><strong> {state.markedOrders}</strong></small>}
                                    </th>
                                    <th>Orden</th>
                                    <th>Cliente</th>
                                    <th>Sucursal</th>
                                    <th>Paquetes</th>
                                    <th>Registro</th>
                                    <th>Chofer</th>
                                    <th>Editar</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    filteredOrders.map(order => 
                                        <tr key={order.id}>
                                            <td><input type="checkbox"  checked={state.selectedOrders.includes(order)} onChange={(event) => handleCheck(event, order)}/></td>
                                            <td>{order.orden}</td>
                                            <td>{mapingCustomer(order)}</td>
                                            <td>{mapingBranch(order)}</td>
                                            <td>{order.paquetes}</td>
                                            <td>{dayjs(order.fecha).format('DD/MM/YYYY hh:mm a')}</td>
                                            <td>{mapingDriver(order)}</td>
                                            <td><IconContainer onClick={() => handleShowEdit(order)}><EditIcon /></IconContainer></td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </Table>
                        <Modal
                            show={showEdit}
                            onHide={handleCloseEdit}
                            backdrop="static"
                            keyboard={false}
                            fullscreen
                        >   
                            <Toaster />
                            <Modal.Body>
                                <Row>
                                    <Col>
                                        <p><strong>ORDEN # {selectedOrder.orden}</strong></p>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Card style={{marginBottom:"0.5rem"}}>
                                            <Card.Body style={{padding:"0.5rem"}}>
                                                <Card.Subtitle>
                                                    Fecha
                                                </Card.Subtitle>
                                                <Card.Text>
                                                    {dayjs(selectedOrder.fecha).format('DD/MM/YYYY')}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                    <Col>
                                        <Card style={{marginBottom:"0.5rem"}}>
                                            <Card.Body style={{padding:"0.5rem"}}>
                                                <Card.Subtitle>
                                                    Cliente
                                                </Card.Subtitle>
                                                <Card.Text>
                                                    {mapingCustomer(selectedOrder)}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Card style={{marginBottom:"0.5rem"}}>
                                            <Card.Body style={{padding:"0.5rem"}}>
                                                <Card.Subtitle>
                                                    Origen
                                                </Card.Subtitle>
                                                <Card.Text>
                                                    {mapingBranch(selectedOrder)}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                    <Col>
                                        <Card style={{marginBottom:"0.5rem"}}>
                                            <Card.Body style={{padding:"0.5rem"}}>
                                                <Card.Subtitle>
                                                    Destino
                                                </Card.Subtitle>
                                                <Card.Text>
                                                    {mapingWarehouse(selectedOrder)}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Card style={{marginBottom:"0.5rem"}}>
                                            <Card.Body style={{padding:"0.5rem"}}>
                                                <Card.Subtitle>
                                                    Chofer
                                                </Card.Subtitle>
                                                <Card.Text>
                                                    {mapingDriver(selectedOrder)}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Card style={{marginBottom:"0.5rem"}}>
                                            <Card.Body style={{padding:"0.5rem"}}>
                                                <Card.Text>
                                                    <AddGuideButton onClick={handleShowAddGuide}>
                                                        <AddIcon />
                                                        <strong>Agregar Guia</strong>
                                                    </AddGuideButton>
                                                    <Modal
                                                        show={showAddGuide}
                                                        onHide={handleCloseAddGuide}
                                                        backdrop="static"
                                                        keyboard={false}
                                                        centered
                                                    >
                                                        <Modal.Body>
                                                            <Row>
                                                                <Col>
                                                                    <strong><p>AGREGAR GUIA</p></strong>
                                                                </Col>
                                                            </Row>
                                                            <Row>
                                                                <Col>
                                                                    <p>Guias pendientes de la sucursal {mapingBranch(selectedOrder)}:</p>
                                                                </Col>
                                                            </Row>
                                                            <Row>
                                                                <Col>
                                                                    <ListGroup>
                                                                        {
                                                                            pendingGuidesToCollect.length > 0 ?
                                                                                pendingGuidesToCollect.map(guide => 
                                                                                    <ListGroup.Item key={guide.id}>
                                                                                        <Row>
                                                                                            <Col>
                                                                                                <strong>{guide.guia}</strong>
                                                                                                <AddGuideButton
                                                                                                    onClick={() => handleAddGuide(guide)}
                                                                                                >
                                                                                                    <AddIcon />
                                                                                                </AddGuideButton>
                                                                                            </Col>
                                                                                        </Row>
                                                                                    </ListGroup.Item>
                                                                                ) : <p>No hay guias registradas</p>
                                                                        }
                                                                    </ListGroup>
                                                                </Col>
                                                            </Row>
                                                        </Modal.Body>
                                                        <Modal.Footer>
                                                            <Button variant="secondary" onClick={handleCloseAddGuide}>
                                                                Cerrar
                                                            </Button>
                                                        </Modal.Footer>
                                                    </Modal>
                                                </Card.Text>
                                                <Card.Subtitle>
                                                    Paquetes:
                                                </Card.Subtitle>
                                                <Card.Text style={{marginTop:"0.5rem"}}>
                                                    <ListContainer>
                                                        { guidesOfSelectedOrder.length > 0 ? guidesOfSelectedOrder.map(guide => 
                                                            <ListGroup>
                                                                <ListGroup.Item key={guide.guia}>
                                                                    <strong>{guide.guia}</strong>
                                                                    {' '}
                                                                    {dayjs(guide.fechaEnvio).format('DD/MM/YYYY hh:mm a')}
                                                                    <RemoveGuideButton onClick={() => handleDeleteGuide(guide)}><RemoveIcon /><strong><small>Eliminar</small></strong></RemoveGuideButton>
                                                                </ListGroup.Item>
                                                            </ListGroup>
                                                        ) : <p>No hay guiasasignadas a esta orden</p>}
                                                    </ListContainer>
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                </Row>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={handleCloseEdit}>
                                    ACEPTAR
                                </Button>
                            </Modal.Footer>
                        </Modal>
                    </TableContainer>
                </Col>
            </Row>
            <Toaster />
        </>
    )
}

export default CollectOrders;