import { useContext } from 'react';
import styled from 'styled-components';
import AppContext from '../context/AppContext';
import { makeStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';

import Logo from '../assets/icono_blanco.svg'

const useStyles = makeStyles({
    root: {
      color: 'white',
    },
  });

const Container = styled.nav`
    position: fixed;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 55px;
    background-color: black;
    color: white;
    width:100%;
`;

const BtnMenu = styled.button`
    box-sizing: border-box;
    border: black solid 1px;
    background-color: transparent;
    margin: 0 15px;

    &:hover {
        border: white solid 1px;
    }
`;

const BadgeContainer = styled.div`
    box-sizing: border-box;
    display: flex;
    align-items: baseline;
    max-width: 100px;
`;

const Badge = styled.img`
    width: 20px;
    & svg {
        fill: red;
    }
`;

const BadgeText = styled.p`
    margin: 0 5px;
    margin-right: 100px;
`;

const Navbar = () => {
    const classes = useStyles();
    const { state: { visibleMenu }, toggleMenu } = useContext(AppContext);

    const handleClick = () => toggleMenu(visibleMenu);

    return (
        <Container>
            <BtnMenu onClick={handleClick}>
                <MenuIcon className={classes.root}/>
            </BtnMenu>
            <BadgeContainer>
                <Badge src={Logo} alt="Logo"/>
                <BadgeText>Monlog</BadgeText>
            </BadgeContainer>
        </Container>
    )
};

export default Navbar;