import { useState } from 'react';
import styled from 'styled-components';
import Layout from "../components/Layout";
import CatalogWarehouse from '../components/CatalogWarehouse';
import CatalogArea from '../components/CatalogArea';
import CatalogDriver from '../components/CatalogDriver';
import CatalogCustomer from '../components/CatalogCustomer';
import CatalogUser from '../components/CatalogUser';
import CatalogZip from '../components/CatalogZip';
import CatalogBranch from '../components/CatalogBranch';
import CatalogParameters from '../components/CatalogParameters';
import CatalogZone from '../components/CatalogZone';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const NavContainer = styled.div`
    width: 90vw;

    overflow-x: scroll;
    ::-webkit-scrollbar {display: none;}
`;

const NavApp = styled.div`
    display: flex;
    align-items: center;
    justify-contetn: space-around;
`;

const NavElement = styled.button`
    background: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-weight: ${props => props.isActive ? "bold;" : "initial;"}

    &:hover {
        font-weight: bold;
    }
`;

const catalogos = [
    {id:1,tittle:'ALMACENES',value:'ALMACENES'},
    {id:2,tittle:'ZONAS',value:'ZONAS'}, 
    {id:3,tittle:'AREAS',value:'AREAS'},
    {id:4,tittle:'CODIGOS POSTALES',value:'CP'}, 
    {id:5,tittle:'CHOFERES',value:'CHOFERES'}, 
    {id:6,tittle:'CLIENTES',value:'CLIENTES'}, 
    {id:7,tittle:'SUCURSALES',value:'SUCURSALES'}, 
    {id:8,tittle:'USUARIOS',value:'USUARIOS'}, 
    {id:9,tittle:'PARAMETROS',value:'PARAMETROS'}
];

const opciones = {};
opciones["ALMACENES"] = <CatalogWarehouse />
opciones["ZONAS"] = <CatalogZone />
opciones["AREAS"] = <CatalogArea />
opciones["CP"] = <CatalogZip />
opciones["CHOFERES"] = <CatalogDriver />
opciones["CLIENTES"] = <CatalogCustomer />
opciones["SUCURSALES"] = <CatalogBranch />
opciones["USUARIOS"] = <CatalogUser />
opciones["PARAMETROS"] = <CatalogParameters />

const Catalogs = () => {

    const [option, setOption] = useState("ALMACENES");

    const handleOption = (event) => {
        setOption(event.target.name)
    };

    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>CATALAGOS</Tittle>
                </Box>
                <NavContainer>
                    <NavApp>
                        {
                            catalogos.map(
                                catalogo => (
                                    <NavElement 
                                        key={catalogo.id} 
                                        name={catalogo.value} 
                                        isActive={catalogo.value === option ? true : false}
                                        onClick={handleOption}
                                    >
                                        {catalogo.tittle}
                                    </NavElement>
                                )
                            )
                        }
                    </NavApp>
                </NavContainer>
                {opciones[option]}
            </Container>
        </Layout>
    )
};

export default Catalogs;