import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc'
import { useState, useRef, useEffect, useReducer } from "react";
import styled from "styled-components";
import PrintIcon from '@material-ui/icons/Print';
import { Table, Form, Modal, Row, Col } from "react-bootstrap";
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import config from '../config';

dayjs.extend(utc)

const headers = [
    {id:0, description: 'posicion'},
    {id: 1, description: 'entrada a almacen'},
    {id: 2, description: 'guia'},
    {id: 3, description: 'codigo postal'},
    {id: 4, description: 'estatus'}
];

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
    width: 60%;
`;

const TableContainerTwo = styled.div`
    height: 300px;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
    width: 100%;
`;

const PrintButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const AssignContainer = styled.div`
    position: relative;
    top: -170px;
    width: 30%;
    height: 300px;
    padding: 10px;
    text-align: center;
`;

const RowSelected = styled.tr`
    background-color: #ddffba
`;

const OptionsButtons = styled.div`
    display: flex;
    width: 100%;
`;

const initialState = {
    markedGuides: 0,
    selectedGuides: []
}

function reducer(state, action) {
    switch (action.type) {
        case "add_one":
            return {
                markedGuides: state.markedGuides+1,
                selectedGuides: state.selectedGuides.concat(action.payload)
            }
        case "substract_one":
            return {
                markedGuides: state.markedGuides-1,
                selectedGuides: state.selectedGuides.filter(selectedGuide => selectedGuide.guia !== action.payload.guia)
            }
        case "add_all":
            return {
                markedGuides: action.payload.length,
                selectedGuides: action.payload
            }
        case "substract_all":
            return {
                markedGuides: 0,
                selectedGuides: []
            }
        case "reorder":
            return {
                ...state,
                selectedGuides: action.payload
            }
        default:
            throw new Error();
    }
}

const reorder = (list, startIndex, endIndex) => {
    const result = [...list];
    const [removed] = result.splice(startIndex,1);
    result.splice(endIndex,0,removed);
    return result;
} 

const zones = [
    {zona: 'Z01', descripcion: 'Monterrey'},
    {zona: 'Z02', descripcion: 'Guadalupe'},
    {zona: 'Z03', descripcion: 'Escobedo'},
    {zona: 'Z04', descripcion: 'Apodaca'}
]

const GuideInWarehouse = () => {
    const [state, dispach] = useReducer(reducer,initialState);
    const [areas,setAreas] = useState([]);
    const [guias,setGuias] = useState([]);
    const [almacenes,setAlmacenes] = useState([]);
    const [choferes,setChoferes] = useState([]);
    const [guiasChofer,setGuiasChofer] = useState([]);
    const [selectedGuide, setSelectedGuide] = useState({});
    const [show, setShow] = useState(false);
    const choferRef = useRef(null);
    const posicionRef = useRef(null);
    const almacenRef = useRef(null);

    const handleClose = () => setShow(false);
    
    const handleShow = async (element) => {
        if(state.selectedGuides.includes(element)) {
            dispach({type: "substract_one", payload: element})
        } else {
            dispach({type: "add_one", payload: element})
        }
    };

    const handleChangeDriver = async () => {
        let assignedGuides = await fetch(`${config.urlBase}guias/consulta/guias/chofer?chofer=${choferRef.current.value}&fecha=${dayjs.utc().format('YYYY-MM-DD')}`)
        assignedGuides = await assignedGuides.json();
        setGuiasChofer(assignedGuides.data);
    };

    const handleAssignmentGuide = async () => {
        let requests = state.selectedGuides.map((selectedGuide, index) => fetch(`${config.urlBase}guias/asignada`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                guia: selectedGuide.guia,
                chofer: choferRef.current.value,
                fecha: dayjs.utc().local().format('YYYY-MM-DDTHH:mm:ss'),
                posicion: index+1
            })
        }));
        let responses = await Promise.all(requests);
        console.log(responses)
        let newGuides = guias;
        dispach({type: "substract_all"})
        state.selectedGuides.map(selectedGuide => newGuides = newGuides.filter(guide => guide.guia !== selectedGuide.guia));
        setGuias(newGuides);
        handleClose();
    };

    const handleUnselectGuide = (event, element) => {
        dispach({type: "substract_one", payload: element})
    }

    useEffect(() => {
        async function fetchData() {
            let responseA = await fetch(`${config.urlBase}areas/`)
            responseA = await responseA.json()
            setAreas(responseA)
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            responseW = await responseW.json();
            setAlmacenes(responseW);
            let responseD = await fetch(`${config.urlBase}choferes/`);
            responseD = await responseD.json();
            setChoferes(responseD);
            let responseG = await fetch(`${config.urlBase}guias/consulta/reporte/guiasxarea/${responseW[0].almacen}`)
            responseG = await responseG.json()
            setGuias(responseG.data[0])
        }
        fetchData()
    },[]);

    const handlePrint = () => {
        let contenedor = document.getElementById('contenedor-print').innerHTML
        let w = window.open();
        w.document.write('<html><head>');
        w.document.write('<style>table{width:100%;border-collapse:collapse;margin:16px 0 16px 0;}table th{border:1px solid #ddd;padding:4px;background-color:#d4eefd;text-align:left;font-size:15px;}table td{border:1px solid #ddd;text-align:left;padding:6px;}</style>');
        w.document.write('</head><body >')
        w.document.write(contenedor);
        w.document.write('</body></html>');
        w.document.close(); // necessary for IE >= 10
        w.focus(); // necessary for IE >= 10
		w.print();
		w.close();
        return true;
    }

    const handleChangeWarehouse = async () => {
        let newGuidesList = await fetch(`${config.urlBase}guias/consulta/reporte/guiasxarea/${almacenRef.current.value}`)
        newGuidesList = await newGuidesList.json();
        setGuias(newGuidesList.data[0]);
    }

    return (
        <DragDropContext onDragEnd={result => {
                const {source, destination} = result;
                if (!destination) {
                    return;
                }
                if (source.index === destination.index && source.droppableId === destination.droppableId) {
                    return;
                }
                let element = reorder(state.selectedGuides,source.index,destination.index)
                dispach({type: "reorder", payload: element})
            }}
        >  
            <Row>
                <Col xs={3}>
                <p>Almacen:</p>
                <Form.Select ref={almacenRef} onChange={handleChangeWarehouse}>
                    {
                        almacenes.map(almacen => <option key={almacen.almacen} value={almacen.almacen}>{almacen.descripcion}</option>)
                    }
                </Form.Select>
                <p></p>
                <OptionsButtons>
                    <PrintButton onClick={handlePrint}><PrintIcon />{' '}IMPRIMIR</PrintButton>
                </OptionsButtons>
                </Col>
            </Row>
            {
                guias.length === 0 &&
                <h4>No se encontraron registros</h4>
            }
            {
                guias.length > 0 &&                            
                <div style={{display: 'flex', justifyContent: 'space-around', padding: "20px"}}>
                    <TableContainer id="contenedor-print">
                        {
                            zones.map(zone => {
                                let areasInZone = areas.filter(area => area.zona === zone.zona);
                                let guidesInZone = guias.filter(guia => guia.zona === zone.zona);
                                if (areasInZone.length > 0 && guidesInZone.length > 0) {
                                    return (
                                        <div key={zone.zona}>
                                            <h4>{zone.zona}: {zone.descripcion}</h4>
                                            {areasInZone.map(area => {
                                                let guidesInArea = guias.filter(guia => guia.area === area.area)
                                                if(guidesInArea.length > 0){
                                                    return (
                                                        <div key={`head-${area.area}`} >
                                                            <h5>{area.area}: {area.descripcion}</h5>
                                                            <Table hover responsive striped bordered>
                                                                <thead>
                                                                    <tr>
                                                                        {headers.map(header => (
                                                                            <th key={header.id}>{header.description}</th>
                                                                        ))}
                                                                    </tr>
                                                                </thead>
                                                                <tbody>
                                                                    {guias.map(guia => {
                                                                        if (area.area === guia.area) {                                                    
                                                                                if(state.selectedGuides.includes(guia)) {
                                                                                    return (
                                                                                        <RowSelected key={guia.guia} onClick={() => handleShow(guia)}>
                                                                                            <td>
                                                                                                {state.selectedGuides.includes(guia) && state.selectedGuides.indexOf(guia)+1}
                                                                                            </td>
                                                                                            <td>
                                                                                                {dayjs.utc(guia.fechaEntregaAlmacen).format('DD/MM/YYYY HH:mm a')}
                                                                                            </td>
                                                                                            <td>
                                                                                                {guia.guia}
                                                                                            </td>
                                                                                            <td>
                                                                                                {guia.codigoPostal}
                                                                                            </td>
                                                                                            <td>
                                                                                                {guia.estatus}
                                                                                            </td>
                                                                                        </RowSelected>
                                                                                    ) 
                                                                                }else {
                                                                                    return (
                                                                                        <tr key={guia.guia} onClick={() => handleShow(guia)}>
                                                                                            <td>
                                                                                                {state.selectedGuides.includes(guia) && state.selectedGuides.indexOf(guia)+1}
                                                                                            </td>
                                                                                            <td>
                                                                                                {dayjs.utc(guia.fechaEntregaAlmacen).format('DD/MM/YYYY HH:mm a')}
                                                                                            </td>
                                                                                            <td>
                                                                                                {guia.guia}
                                                                                            </td>
                                                                                            <td>
                                                                                                {guia.codigoPostal}
                                                                                            </td>
                                                                                            <td>
                                                                                                {guia.estatus}
                                                                                            </td>
                                                                                        </tr>
                                                                                    ) 
                                                                                }
                                                                        }
                                                                        return 
                                                                    })}
                                                                </tbody>
                                                            </Table>
                                                        </div>
                                                    )
                                                }
                                                return 
                                            })}
                                        </div>
                                    )
                                }
                                return <></>
                            })
                        }
                    </TableContainer>
                    <AssignContainer>
                        {
                            state.selectedGuides.length > 0 &&
                            <CustomButton variant="primary" onClick={handleAssignmentGuide}>
                                ASIGNAR
                            </CustomButton>
                        }
                        <Form.Group className="mb-3">
                            <Form.Label><strong>CHOFER:</strong></Form.Label>
                            <Form.Select ref={choferRef} onChange={handleChangeDriver}>
                                {choferes.map(chofer => <option key={chofer.chofer} value={chofer.chofer}>{chofer.nombre}</option>)}
                            </Form.Select>
                        </Form.Group>
                        <TableContainerTwo>
                            <Table hover responsive striped bordered>
                                <thead>
                                    <tr>
                                        <th>
                                            posicion
                                        </th>
                                        <th>
                                            guia
                                        </th>
                                        <th>
                                            CP
                                        </th>
                                    </tr>
                                </thead>
                                <Droppable droppableId="choferGuias">
                                {(droppableProvider) => (
                                    <tbody 
                                        {...droppableProvider.droppableProps}
                                        ref={droppableProvider.innerRef}
                                    >
                                        {
                                            state.selectedGuides.map((guide,index) => (
                                                <Draggable key={guide.guia} draggableId={guide.guia} index={index}>
                                                    {
                                                        (draggableProvider) => (
                                                            <tr
                                                                {...draggableProvider.draggableProps}
                                                                ref={draggableProvider.innerRef}
                                                                {...draggableProvider.dragHandleProps}
                                                                onClick={(e) => handleUnselectGuide(e,guide)}
                                                            >
                                                                <td>
                                                                    {index+1}
                                                                </td>
                                                                <td>
                                                                    {guide.guia}
                                                                </td>
                                                                <td>
                                                                    {guide.codigoPostal}
                                                                </td>
                                                            </tr>
                                                        )
                                                    }
                                                </Draggable>
                                            ))
                                        }
                                        {droppableProvider.placeholder}
                                    </tbody>
                                )}
                                </Droppable>
                            </Table>
                        </TableContainerTwo>
                    </AssignContainer>
                </div>
            }
            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
                fullscreen
            >
                <Modal.Header closeButton>
                    <Modal.Title>ASIGNACION DE GUIA</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                <Form.Group className="mb-3">
                    <Form.Label>Chofer:</Form.Label>
                    <Form.Select ref={choferRef} onChange={handleChangeDriver}>
                        {choferes.map(chofer => <option key={chofer.chofer} value={chofer.chofer}>{chofer.nombre}</option>)}
                    </Form.Select>
                </Form.Group>
                <Row className="mb-3">
                    <Form.Group as={Col}>
                        <Form.Label>Guia por asignar:</Form.Label>
                        <Form.Control readOnly value={selectedGuide.guia} />
                    </Form.Group>
                    <Form.Group as={Col}>
                        <Form.Label>Posicion:</Form.Label>
                        <Form.Control type="number" min={0} ref={posicionRef}/>
                    </Form.Group>
                </Row>
                <Form.Group>
                    <Form.Label>Guias asignadas:</Form.Label>
                    {
                        guiasChofer.length > 0 &&
                        <div id="tabla-sorteable-chofer">
                            <Table striped bordered hover responsive>
                            <thead>
                                <tr>
                                    <th colSpan="2">Guia</th>
                                    <th>Posicion</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    guiasChofer.map(guiaChofer => (        
                                        <tr key={guiaChofer.guia}>
                                            <td colSpan="2">{guiaChofer.guia}</td>
                                            <td>{guiaChofer.posicion}</td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                            </Table>
                        </div>
                    }
                    {
                        guiasChofer.length === 0 &&
                        <h6>Aun no cuenta con guias asignadas.</h6>
                    }
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton variant="primary" onClick={handleAssignmentGuide}>
                        ASIGNAR
                    </CustomButton>
                    <CustomButtonOutline variant="secondary" onClick={handleClose}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
        </DragDropContext>
    )
};

export default GuideInWarehouse;