import Quagga from 'quagga';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import { useState, useEffect, useContext, useRef } from 'react';
import styled from 'styled-components';
import AppContext from '../context/AppContext';
import Layout from "../components/Layout";
import { Table, Modal, Form, Spinner, InputGroup, FormControl } from 'react-bootstrap';
import CropFreeIcon from '@material-ui/icons/CropFree';
import toast, { Toaster } from 'react-hot-toast';
import SignatureCanvas from 'react-signature-canvas'

import config from '../config';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const TextModal = styled.p`
    margin-bottom: 5px;
`;

const CameraArea = styled.div`
    width: 90vw;
`;

const ScanCodeButton = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    border: none;
    color: white;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 10px;
    padding: 10px;
`;

const SignField = styled.div`
    max-width: 360px;
    border: solid black 2px;
`;

dayjs.extend(utc);

const DriverOrders = () => {
    const { state: { user } } = useContext(AppContext);
    const [orders, setOrders] = useState([]);
    const [branches, setBranches] = useState([]);
    const [customers, setCustomers] = useState([]);
    const [selectedElement, setSelectedElement] = useState({});
    const [scanGuide, setScanGuide]= useState("");
    const [show, setShow] = useState(false);
    const [showGuideModal, setShowGuideModal] =  useState(false);
    const [showMessage, setShowMessage] = useState(false);
    const [guidesCollected, setGuidesCollected] = useState([]);
    const [quaggaInit, setQuaggaInit] = useState(false);
    const signCanvas = useRef({});
    const nombreFirmaRef = useRef(null);

    useEffect(() => {
        async function fetchOrders() {
            let responseO = await fetch(`${config.urlBase}ordenes/asignadas/all/${user.chofer}`);
            responseO = await responseO.json();
            setOrders(responseO.data);
            let responseB = await fetch(`${config.urlBase}sucursales/`);
            responseB = await responseB.json();
            setBranches(responseB.data);
            let responseC = await fetch(`${config.urlBase}clientes/`);
            responseC = await responseC.json();
            setCustomers(responseC);
        }
        fetchOrders()
    },[user.chofer]);

    const handleClose = () => {
        if(quaggaInit) {
            Quagga.stop()
        }
        setShow(false);
    }

    const handleShow = async (element) => {
        setSelectedElement(element);
        let responseG = await fetch(`${config.urlBase}guias/consulta/guias/recolectadas?orden=${element.orden}`);
        responseG = await responseG.json();
        setGuidesCollected(responseG.data);
        setShow(true);
    }

    const handleGuideModalClose = () => {
        setShowGuideModal(false);
    }

    const handleGuideModalShow = () => {
        setShowGuideModal(true);
    }

    const handleChangeGuide = (event) => {
        setScanGuide(event.target.value)
    }

    const handleGuideRegistration = async () => {
        let postObject = {
            orden: selectedElement.orden,
            guia: scanGuide,
            fecha: dayjs.utc().local().format('YYYY-MM-DDTHH:mm:ss')
        }
        let response = await fetch(`${config.urlBase}guias/recolectada`,{
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json'
              },
            body: JSON.stringify(postObject)
        });
        response = await response.json()
        console.log(response)
        setScanGuide("");
        setGuidesCollected(guidesCollected.concat(response.data))
        handleGuideModalClose();
    }

    const handleOrderRegistration = async () => {
        try {
            if(!nombreFirmaRef.current.value){
                throw Error('El nombre y la firma deben ser registrados')
            }
            if (signCanvas.current.isEmpty()) {
                throw Error('El nombre y la firma deben ser registrados')
            }
            let response = await fetch(`${config.urlBase}ordenes/marcar/recolectada`,{
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json'
                  },
                body: JSON.stringify({
                    orden: selectedElement.orden,
                    quienValida: nombreFirmaRef.current.value,
                    firmaValida: signCanvas.current.getTrimmedCanvas().toDataURL("image/png").replace(/^data:image\/[a-z]+;base64,/, "")
                })
            });
            response = await response.json()
            setOrders(orders.filter(order => order.orden !== selectedElement.orden));
            handleClose();
            toast.success(response.message)
        } catch (error) {
            console.log(error)
            toast.error(error.message)
        }
    };

   Quagga.onDetected(function(result) {
       setScanGuide(`${result.codeResult.code}`);
       handleGuideModalShow();
    });

    const handleBarCodeReader = () => {
        setShowMessage(true);
        Quagga.init({
            inputStream: {
                type : "LiveStream",
                target: document.querySelector('#camera'),
                constraints: {
                    aspectRatio: {min: 0.70, max: 0.70},
                    facingMode: "environment" // or user
                }
            },
            locator: {
                patchSize: "medium",
                halfSample: true
            },
            numOfWorkers: 4,
            frequency: 2,
            decoder: {
                readers : [{
                    format: "code_128_reader",
                    config: {}
                }]
            },
            locate: true
        }, function(err) {
              if (err) {
                  console.log(err);
                  return
              }
              console.log("Initialization finished. Ready to start");
              Quagga.start();
              setQuaggaInit(true);
              setShowMessage(false);
          });
    }

    const handleCleanSignature = () => {
        signCanvas.current.clear();
    }

    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>SOLICITUDES DE RECOLECCION</Tittle>
                </Box>
                {
                    orders.length > 0 &&
                    <h6 className="mb-3">TOTAL DE ORDENES: {orders.length}</h6>
                }
                {
                    orders.length > 0 && (
                        <TableContainer>
                            <Table hover responsive striped bordered>
                                <thead>
                                    <tr>
                                        <th>cliente</th>
                                        <th>sucursal</th>
                                        <th>direccion</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {orders.map(order => (
                                        <tr key={order.orden} onClick={() => handleShow(order)}>
                                            <td>
                                                {customers.map(customer => customer.cliente === order.cliente ? customer.nombreCorto : "")}
                                            </td>
                                            <td>
                                                {branches.map(branch => branch.sucursal === order.sucursal ? branch.descripcion : "")}
                                            </td>
                                            <td>
                                                {branches.map(branch => branch.sucursal === order.sucursal ? `${branch.direccion} ${branch.numeroExt}, ${branch.colonia}, ${branch.numeroInt}` : "")}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </Table>
                        </TableContainer>
                    )
                }
                {
                    orders.length === 0 && <h5>No cuenta con ordenes asignadas</h5>
                }
                <Modal
                    show={show}
                    onHide={handleClose}
                    backdrop="static"
                    keyboard={false}
                    fullscreen
                >
                    <Modal.Header closeButton>
                    <Modal.Title>DETALLE DE ORDEN</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <TextModal><strong>ORDEN:</strong> {selectedElement.orden}</TextModal>
                        <TextModal><strong>{branches.map( branch => branch.sucursal === selectedElement.sucursal ? branch.descripcion.toUpperCase() : "")}</strong></TextModal>
                        <TextModal><strong>FECHA:</strong> {dayjs(selectedElement.fecha).format('DD/MM/YYYY')}</TextModal>
                        <TextModal><strong>PAQUETES:</strong> {selectedElement.paquetes}</TextModal>
                        <p>Guias recolectadas de la orden:</p>
                        <ul>
                            {
                                guidesCollected.length > 0 &&
                                guidesCollected.map(guide => <li key={guide.guia}>{guide.guia}</li>)
                            }
                        </ul>
                        <ScanCodeButton onClick={handleBarCodeReader}>
                            <CropFreeIcon />
                            {" "}
                            ESCANEAR
                        </ScanCodeButton>
                        {
                            showMessage && (
                                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
                                    <p></p>
                                    <Spinner animation="border" variant="warning" />
                                    <p><strong>CARGANDO</strong></p>
                                </div>
                            )
                        }
                        <CameraArea id="camera"></CameraArea>
                        {
                            guidesCollected.length === selectedElement.paquetes &&

                            <SignField>
                                  <InputGroup className="mb-3">
                                    <InputGroup.Text id="inputGroup-sizing-default">Nombre:</InputGroup.Text>
                                    <FormControl
                                        ref={nombreFirmaRef}
                                    />
                                </InputGroup>
                                <SignatureCanvas
                                    ref={signCanvas} 
                                    canvasProps={{width: 'inherit', height: 125}}
                                />
                                <button onClick={handleCleanSignature} >Limpiar</button>
                            </SignField>
                        }
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButton variant="primary" onClick={handleOrderRegistration}>
                        REGISTRAR
                    </CustomButton>
                    <CustomButtonOutline variant="secondary" onClick={handleClose}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                    <Toaster/>
                </Modal>
                <Modal
                    show={showGuideModal}
                    onHide={handleGuideModalClose}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                    <Modal.Title>GUIA POR REGISTRAR</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>El siguiente numero de gua sera registrado</Form.Label>
                            <Form.Control type="text" onChange={handleChangeGuide} value={scanGuide}/>
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButton variant="primary" onClick={handleGuideRegistration}>
                        REGISTRAR
                    </CustomButton>
                    <CustomButtonOutline variant="secondary" onClick={handleGuideModalClose}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                </Modal>
                <Toaster/>
            </Container>
        </Layout>
    )
};

export default DriverOrders;