import dayjs from 'dayjs';
import { useState, useRef, useEffect } from "react";
import styled from "styled-components";
import { InputGroup, FormControl, Button, Form, Row, Col, Modal, Table, FloatingLabel  } from "react-bootstrap";
import toast, { Toaster } from 'react-hot-toast';
import CropFreeIcon from '@material-ui/icons/CropFree';
import CheckIcon from '@material-ui/icons/Check';

import config from '../config';

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const GuideRegistration = () => {
    const [show, setShow] = useState(false);
    const [guide, setGuide] = useState({});
    const [guides, setGuides] = useState([]);
    const [orders, setOrders] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const [driver, setDriver] = useState({});
    const guiaRef = useRef(null);
    const choferRef = useRef(null);

    const handleEditChange = (event) => {
        setGuide({
            ...guide,
            [event.target.name]: event.target.value
        });
    };

    const handleEnter = async (event) => {
        if(event.key === "Enter") {
            if(guiaRef.current.value === "") return;
            let response = await fetch(`${config.urlBase}guias/consulta/guia?guia=${guiaRef.current.value}`);
            response = await response.json();
            response = response.data
            if(!response || response.estatus !== 'Recolectada') {
                toast.error('Guia no marcada como recolectada');
            } else {
                response = {
                    ...response,
                    fechaEntregaAlmacen: dayjs().local().format('YYYY-MM-DDTHH:mm:ss')
                }
                setGuide(response);
                handleShow();
            }
        }
    };

    const handleClick = async () => {
        if(guiaRef.current.value === "") return
        let response = await fetch(`${config.urlBase}guias/consulta/guia?guia=${guiaRef.current.value}`);
        response = await response.json();
        response = response.data
        if(!response || response.estatus !== 'Recolectada') {
            toast.error('Guia no marcada como recolectada');
        } else {
            response = {
                ...response,
                fechaEntregaAlmacen: dayjs().local().format('YYYY-MM-DDTHH:mm:ss')
            }
            setGuide(response);
            handleShow();
        }
    };

    const handleModalClose = () => {
        guiaRef.current.value = "";
        handleClose();
    };

    const handleRegistry = async () => {
        let response = await fetch(`${config.urlBase}guias/almacen`,{
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json'
              },
              body: JSON.stringify(guide)
        });
        response = await response.json();
        guiaRef.current.value = "";
        setGuides(guides.filter(guia => guia.guia !== guide.guia))
        handleClose();
        toast.success(response.message)
    };

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const handleChangeDriver = () => {
        setDriver(choferRef.current.value)
    }

    useEffect(() => {
        async function fetchData() {
            let choferes = await fetch(`${config.urlBase}choferes/`);
            choferes = await choferes.json();
            setDrivers(choferes);
            setDriver(choferes[0].chofer);
            let guias = await fetch(`${config.urlBase}guias/consulta/guias/recolectadas-all`);
            guias = await guias.json();
            guias = guias.data;
            setGuides(guias);
            let ordenes = await fetch(`${config.urlBase}ordenes/recolectadas/all`);
            ordenes = await ordenes.json();
            ordenes = ordenes.data;
            setOrders(ordenes)
        }
        fetchData();
    },[])

    return (
        <>
        <Row>
            <Col xs={6}>
                <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon1"># Guia</InputGroup.Text>
                    <FormControl
                        aria-label="Guia"
                        aria-describedby="basic-addon1"
                        autoFocus
                        ref={guiaRef}
                        onKeyUp={handleEnter}
                    />
                    <Button variant="outline-secondary" id="button-addon2" className="d-sm-none">
                        <CropFreeIcon /> 
                        <small>ESCANEAR</small>
                    </Button>
                    <Button variant="outline-secondary" id="button-addon2" onClick={handleClick} className="d-none d-sm-block">
                        <CheckIcon /> 
                        <small>REGISTRAR</small>
                    </Button>
                </InputGroup>
            </Col>
        </Row>
        <Row>
            <Col>
                <FloatingLabel controlId="floatingSelect" label="CHOFER">
                    <Form.Select aria-label="Default select example" ref={choferRef} onChange={handleChangeDriver}>
                        {drivers.map(driver => <option value={driver.chofer}>{driver.nombre}</option>)}
                    </Form.Select>
                </FloatingLabel>
                <Table responsive striped bordered className="mt-4">
                    {orders.map( order => {
                        if (order.choferRecoleccion === driver) {
                            return (
                                <>
                                <thead>
                                    <tr>
                                        <th>orden {order.orden}</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        guides.map(guide => {
                                            if (guide.orden === order.orden) {
                                                return (
                                                    <tr key={guide.guia}>
                                                        <td>
                                                            {guide.guia}
                                                        </td>
                                                        <td>
                                                            {guide.estatus}
                                                        </td>
                                                    </tr>
                                                )
                                            } else {
                                                return <></>
                                            }
                                        })
                                    }
                                </tbody>
                                </>
                            )
                        } else {
                            return <></>
                        }
                    })}
                </Table>
            </Col>
        </Row>
        <Modal
            show={show}
            onHide={handleClose}
            backdrop="static"
            keyboard={false}
        >
        <Modal.Header closeButton>
          <Modal.Title>ENTRADA DE GUIA A ALMACEN</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form>
            <Row>
                <Col xs={3}>
                    <Form.Label>Orden</Form.Label>
                    <Form.Control readOnly value={guide.orden}/>
                </Col>
                <Col>
                    <Form.Label>Guia</Form.Label>
                    <Form.Control readOnly value={guide.guia}/>
                </Col>
                <Col>
                    <Form.Label># De Pedido</Form.Label>
                    <Form.Control value={guide.numeroPedido || ""} onChange={handleEditChange} name="numeroPedido"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form.Label>Nombre del cliente</Form.Label>
                    <Form.Control value={guide.nombreClienteFinal || ""} onChange={handleEditChange} name="nombreClienteFinal"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form.Label>Calle</Form.Label>
                    <Form.Control value={guide.direccion || ""} onChange={handleEditChange} name="direccion"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form.Label>Numero Exterior</Form.Label>
                    <Form.Control value={guide.numeroExt || ""} onChange={handleEditChange} name="numeroExt"/>
                </Col>
                <Col>
                    <Form.Label>Numero Interior</Form.Label>
                    <Form.Control value={guide.numeroInt || ""} onChange={handleEditChange} name="numeroInt"/>
                </Col>
                <Col>
                    <Form.Label>Codigo Postal</Form.Label>
                    <Form.Control value={guide.codigoPostal || ""} onChange={handleEditChange} name="codigoPostal"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form.Label>Referencias de ubicacion</Form.Label>
                    <Form.Control value={guide.referencias || ""} onChange={handleEditChange} name="referencias"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form.Label>Colonia</Form.Label>
                    <Form.Control value={guide.colonia || ""} onChange={handleEditChange} name="colonia"/>
                </Col>
                <Col>
                    <Form.Label>Telefono</Form.Label>
                    <Form.Control value={guide.telefono || ""} onChange={handleEditChange} name="telefono"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form.Label>Estado</Form.Label>
                    <Form.Control value={guide.estado || ""} onChange={handleEditChange} name="estado"/>
                </Col>
                <Col>
                    <Form.Label>Ciudad</Form.Label>
                    <Form.Control value={guide.ciudad || ""} onChange={handleEditChange} name="ciudad"/>
                </Col>
            </Row>
        </Form>
        </Modal.Body>
        <Modal.Footer>
          <CustomButtonOutline variant="secondary" onClick={handleModalClose}>
            CANCELAR
          </CustomButtonOutline>
          <CustomButton variant="primary" onClick={handleRegistry}>REGISTRAR</CustomButton>
        </Modal.Footer>
      </Modal>
      <Toaster />
      </>
    )
};

export default GuideRegistration;