import { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Table, Modal,InputGroup, FormControl } from 'react-bootstrap';

import config from '../config';

const TableContainer = styled.div`
    margin: auto;
    width: 780px;
    height: 300px;
    overflow-y: scroll;
    @media (max-width: 860px) {
        width: 780px;
    }
    @media (max-width: 760px) {
        width: 580px;
    }
    @media (max-width: 560px) {
        width: 380px;
    }
    @media (max-width: 360px) {
        width: 280px;
    }
`;

const DeleteButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: red;
    }
`;

const EditButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: #fdd448;
    }
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CatalogZone = () => {
    const [elements, setElements] = useState([]);
    const [selectElement, setSelectElement] = useState({});
    const [showAddModal, setShowAddModal] = useState(false);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const codigoAgregarRef = useRef(null);
    const descripcionAgregarRef = useRef(null);

    useEffect(()=> {
        async function fetchData(){
            let response = await fetch(`${config.urlBase}zonas/`);
            let zonas = await response.json();
            setElements(zonas)
        }
        fetchData()
    },[]);

    const handleCloseAddModal = () => setShowAddModal(false);
    const handleShowAddModal = () => setShowAddModal(true);
    const handleCloseEditModal = () => setShowEditModal(false);
    const handleShowEditModal = (element) => {
        setSelectElement(element);
        setShowEditModal(true);
    };
    const handleCloseDeleteModal = () => setShowDeleteModal(false);
    const handleShowDeleteModal = (element) => {
        setSelectElement(element);
        setShowDeleteModal(true);
    };
    

    const handleDelete = async () => {
        let response = await fetch(`${config.urlBase}zonas/eliminar?zona=${selectElement.zona}`,{
            method: 'DELETE'
        });
        let data = await response.json();
        console.log(data);
        setElements(elements.filter(element => element.zona !== selectElement.zona));
        setShowDeleteModal(false);
    };

    const handleAdd = async () => {
        let newElement = {};
        newElement.zona = codigoAgregarRef.current.value;
        newElement.descripcion = descripcionAgregarRef.current.value;
        let response = await fetch(`${config.urlBase}zonas/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        setElements(elements.concat(response.data))
        setShowAddModal(false);
    };

    const handleEditChange = (event) => {
        setSelectElement({
            ...selectElement,
            descripcion: event.target.value
        });
    };

    const handleEdit = async () => {
        let newElement = {};
        newElement.zona = selectElement.zona;
        newElement.descripcion = selectElement.descripcion;
        newElement.activo = selectElement.activo;
        let response = await fetch(`${config.urlBase}zonas/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        let newsElements = elements;
        newsElements.map( element => {
            if (element.zona === selectElement.zona) {
                element.zona = response.data.zona;
                element.descripcion = response.data.descripcion;
            }
            return element
        })
        setElements(newsElements)
        setShowEditModal(false);
    };

    return (
        <>
            <CustomButton onClick={handleShowAddModal}>AGREGAR</CustomButton>
            <Modal
                show={showAddModal}
                onHide={handleCloseAddModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>AGREGAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text 
                            id="inputGroup-sizing-sm">Codigo</InputGroup.Text>
                        <FormControl 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            ref={codigoAgregarRef} 
                            maxLength="10"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Descripcion</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={descripcionAgregarRef} maxLength="100"/>
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleAdd} >AGREGAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseAddModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showEditModal}
                onHide={handleCloseEditModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>EDITAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo</InputGroup.Text>
                        <FormControl 
                            disabled 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            value={selectElement.zona}
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Descripcion</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="100" 
                            value={selectElement.descripcion}
                            onChange={handleEditChange}
                        />
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleEdit} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseEditModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showDeleteModal}
                onHide={handleCloseDeleteModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>CONFIRMACION NECESARIA!</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {`Al precionar aceptar, el registro ${selectElement.zona} ser elminado permanentemente, desea continuar?`}
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleDelete} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseDeleteModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <TableContainer>
                <h3>ZONAS</h3>
                <Table striped hover responsive>
                    <thead>
                        <tr>
                        <th>Codigo</th>
                        <th>Descripcion</th>
                        <th>{" "}</th>
                        <th>{" "}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            elements.length > 0 && elements.map( zona => (
                                <tr key={zona.zona}>
                                    <td>
                                        {zona.zona}
                                    </td>
                                    <td>{zona.descripcion}</td>
                                    <td>
                                        <EditButton onClick={()=>handleShowEditModal(zona)}>
                                            <EditIcon />
                                        </EditButton>
                                    </td>
                                    <td>
                                        <DeleteButton onClick={()=>handleShowDeleteModal(zona)}>
                                            <DeleteIcon />
                                        </DeleteButton>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </Table>
                {
                    elements.length === 0 && <h1>No hay datos por mostrar</h1>
                }
            </TableContainer>
        </>
    )
};

export default CatalogZone;