import Quagga from 'quagga';
import { useState, useContext, useEffect } from 'react';
import dayjs from 'dayjs';
import styled from 'styled-components';
import { Table, Modal, Spinner, Alert } from 'react-bootstrap';
import toast, { Toaster } from 'react-hot-toast';
import AppContext from '../context/AppContext';
import Layout from "../components/Layout";

import config from '../config';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const TextModal = styled.p`
    margin-bottom: 5px;
`;

const CameraArea = styled.div`
    width: 90vw;
`;

const ScanCodeButton = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    border: none;
    color: white;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 10px;
    padding: 10px;
`;

const opciones = {};
opciones["ENTRADA"] = <h1>GUIA</h1>

const DriverReturns = () => {
    const { state: { user } } = useContext(AppContext);
    const [guides,setGuides] = useState([]);
    const [selectedElement, setSelectedElement] = useState({});
    const [show, setShow] = useState(false);
    const [showModalVerify, setShowModalVerify] = useState(false);
    const [quaggaInit, setQuaggaInit] = useState(false);
    const [showMessage, setShowMessage] = useState(false);
    const [badGuideMessage, setBadGuideMessage] = useState(false);

    useEffect(() => {
        async function fetchData() {
            console.log(dayjs.utc().local().format('YYYY-MM-DD'))
            let responseG = await fetch(`${config.urlBase}guias/consulta/guias/chofer-devolucion?chofer=${user.chofer}&fecha=${dayjs.utc().local().format('YYYY-MM-DD')}`);
            responseG = await responseG.json();
            setGuides(responseG.data);
        }
        fetchData()
    },[user.chofer]);

    const handleOrderRegistration = async () => {
        let newBody = {
            orden: selectedElement.orden,
            guia: selectedElement.guia,
            fecha: dayjs.utc().local().format('YYYY-MM-DDTHH:mm:ss'),
        }
        let responseRG = await fetch(`${config.urlBase}guias/devuelta-cliente`,{
            method: 'POST',
            mode: 'cors',
            headers: {
            'Content-Type': 'application/json'
            },
            body: JSON.stringify(newBody)
        });
        responseRG = await responseRG.json();
        setGuides(guides.filter(guide => guide.guia !== selectedElement.guia));
        handleClose();
        toast.success(responseRG.message);
    }

    const handleClose = () => {
        setBadGuideMessage(false);
        setShow(false);
    }

    const handleShow = () => {
        setShow(true);
    }

    const handleShowModalVerify = (element) => {
        setSelectedElement(element);
        setShowModalVerify(true)
    }

    const handleCloseModalVerify = () => {
        if(quaggaInit) {
            Quagga.stop()
        }
        setBadGuideMessage(false);
        setShowModalVerify(false);
    }

    Quagga.onDetected(function(result) {
        if(result.codeResult.code === selectedElement.guia) {
            Quagga.stop();
            setShowModalVerify(false);
            handleShow();
        } else {
            setBadGuideMessage(true);
            Quagga.stop()
        }
     });

    const handleVerify = () => {
        setBadGuideMessage(false);
        setShowMessage(true);
        Quagga.init({
            inputStream: {
                type : "LiveStream",
                target: document.querySelector('#camerdevolcuincliente'),
                constraints: {
                    aspectRatio: {min: 0.70, max: 0.70},
                    facingMode: "environment" // or user
                }
            },
            locator: {
                patchSize: "medium",
                halfSample: true
            },
            numOfWorkers: 4,
            frequency: 1,
            decoder: {
                readers : [{
                    format: "code_128_reader",
                    config: {}
                }]
            },
            locate: true
        }, function(err) {
              if (err) {
                  console.log(err);
                  return
              }
              console.log("Initialization finished. Ready to start");
              Quagga.start();
              setQuaggaInit(true);
              setShowMessage(false);
          });
    }

    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>GUIAS A DEVOLVER</Tittle>
                </Box>
                {
                    guides.length > 0 && (
                        <TableContainer>
                            <Table hover responsive striped bordered>
                                <thead>
                                    <tr>
                                        <th>nombre</th>
                                        <th>direccion</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {guides.map(guide => (
                                        <tr key={guide.guia} onClick={() => handleShowModalVerify(guide)}>
                                            <td>
                                                {guide.nombreCorto}
                                            </td>
                                            <td>
                                                {`${guide.direccion}, Col. ${guide.colonia}, ${guide.ciudad}, ${guide.estado}. ${guide.codigoPostal}`}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </Table>
                        </TableContainer>
                    )
                }
                {
                    guides.length === 0 && <h5>No cuenta con guias asignadas</h5>
                }
                <Modal
                    show={show}
                    onHide={handleClose}
                    backdrop="static"
                    keyboard={false}
                    fullscreen
                >
                    <Modal.Header closeButton>
                    <Modal.Title>DETALLE DE GUIA</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <TextModal><strong>GUIA:</strong> {selectedElement.guia}</TextModal>
                        <TextModal><strong>CLIENTE:</strong> {selectedElement.nombreCorto}</TextModal>
                        <Alert variant="warning">
                            <Alert.Heading>Aviso</Alert.Heading>
                            <p>
                            Estas a punto de <strong>marcar</strong> la guia como <strong>devuelta al cliente.</strong><br/>
                            Esta accion es irreversible por lo que te aconsejamos validar los datos antes de dar clic en <strong>DEVOLVER</strong>
                            </p>
                        </Alert>
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButton variant="primary" onClick={handleOrderRegistration}>
                        DEVOLVER
                    </CustomButton>
                    <CustomButtonOutline variant="secondary" onClick={handleClose}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={showModalVerify}
                    onHide={handleCloseModalVerify}
                    backdrop="static"
                    keyboard={false}
                    fullscreen
                >
                    <Modal.Header closeButton>
                    <Modal.Title>DEVOLUCION DE GUIA</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <TextModal>Para poder realizar la devolucion del paquete es necesario confirmar el numero de guia. Por favor presione escanear para verificarlo.</TextModal>
                        <ScanCodeButton onClick={handleVerify}>ESCANEAR</ScanCodeButton>
                        {
                            showMessage && (
                                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
                                    <p></p>
                                    <Spinner animation="border" variant="warning" />
                                    <p><strong>CARGANDO</strong></p>
                                </div>
                            )
                        }
                        {
                            badGuideMessage && 
                                <Alert variant="danger">
                                    <Alert.Heading>Atencion!</Alert.Heading>
                                    <p>
                                        El numero de guia no corresponde al paquete por devolver. <br/>
                                        Para intentarlo de nuevo presione escanear.
                                    </p>
                                </Alert>
                        }
                        <CameraArea id="camerdevolcuincliente"></CameraArea>
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButtonOutline variant="secondary" onClick={handleCloseModalVerify}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                </Modal>
                <Toaster />
            </Container>
        </Layout>
    )
};

export default DriverReturns;