import { useState } from "react";
import styled from "styled-components";

import Layout from "../components/Layout";
import CollectOrders from "../components/Guides/CollectOrders";
import HandleGuides from "../components/Guides/HandleGuides";
import ToCollect from "../components/Guides/ToCollect";
import InWarehouse from "../components/Guides/InWarehouse";
import TrackingGuides from "../components/Guides/TrackingGuides";


const options = {
    TOCOLLECT: <ToCollect />,
    COLLECTORDERS: <CollectOrders />,
    HANDLEGUIDES: <HandleGuides />,
    INWAREHOUSE: <InWarehouse />,
    TRACKINGGUIDES: <TrackingGuides />
};

const navOptions = [
    { id:1, name:'GUIAS POR RECOLECTAR',value:'TOCOLLECT'},
    { id:2, name:'ORDENES DE RECOLECCION',value:'COLLECTORDERS'},
    { id:3, name:'ENTRADA/DEVOLUCION DE GUIAS',value:'HANDLEGUIDES'},
    { id:4, name:'GUIAS EN ALMACEN',value:'INWAREHOUSE'},
    { id:5, name:'TRACKING DE GUIAS',value:'TRACKINGGUIDES'}
];

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const NavContainer = styled.div`
    width: 90vw;
    margin-bottom: 10px;

    overflow-x: scroll;
    ::-webkit-scrollbar {display: none;}
`;

const NavApp = styled.div`
    display: flex;
    align-items: center;
    justify-contetn: space-around;
`;

const NavElement = styled.button`
    background: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-weight: ${props => props.isActive ? "bold;" : "initial;"}

    &:hover {
        font-weight: bold;
    }
`;

const ApplicationFull = () => {
    const [page, setPage] = useState("TOCOLLECT");

    return (
        <Layout>
            <Container>
                <NavContainer>
                    <NavApp>
                        {navOptions.map(option => (
                            <NavElement
                                key={option.id}
                                name={option.name}
                                isActive={page === option.value}
                                onClick={() => setPage(option.value)}
                            >
                                {option.name}
                            </NavElement>
                        ))}
                    </NavApp>
                </NavContainer>
                {
                    options[page] 
                }  
            </Container>
        </Layout>
    )
}

export default ApplicationFull;