import dayjs from 'dayjs';
import styled from "styled-components";
import { useEffect, useState, useRef } from 'react';
import { Table, Row, Col, InputGroup, FormControl, FloatingLabel, Form } from 'react-bootstrap';

import config from '../config';

const headers = [
    {id: 1, description: 'orden'},
    {id: 2, description: 'fecha'},
    {id: 3, description: 'sucursal'},
    {id: 4, description: 'paquetes'},
    {id: 5, description: 'estatus'},
    {id: 6, description: 'chofer'}
];

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
`;

const TableInWarehouseOrder = () => {
    let fecha = new Date();
    const [orders, setOrders] = useState([]);
    const [branches, setBranches] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const [warehouses, setWarehouses] = useState([]);
    const [activeWarehouse,setActiveWarehouse] = useState("")
    const [selectedInitDate, setSelectedInitDate] = useState(dayjs.utc(fecha).local().format('YYYY-MM-DD'))
    const [selectedFinDate, setSelectedFinDate] = useState(dayjs.utc(fecha).local().format('YYYY-MM-DD'))
    const dateIniRef = useRef(null);
    const dateFiniRef = useRef(null);
    const warehouseRef = useRef(null);
  
    useEffect(() => {
        async function fetchOrders() {
            let response = await fetch(`${config.urlBase}ordenes/almacen/all?fechaIni=${selectedInitDate}&fechaFin=${selectedFinDate}`)
            response = await response.json()
            setOrders(response.data)
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            responseW = await responseW.json();
            setWarehouses(responseW);
            setActiveWarehouse(responseW[0].almacen);
            let responseB = await fetch(`${config.urlBase}sucursales/`)
            responseB = await responseB.json()
            setBranches(responseB.data)
            let responseD = await fetch(`${config.urlBase}choferes/`)
            responseD = await responseD.json()
            setDrivers(responseD)
        }
        fetchOrders()
    },[selectedInitDate,selectedFinDate]);

    const handleChangeDate = async (event) => {
        let dateIni = new Date(dateIniRef.current.value)
        dateIni.setDate(dateIni.getDate()+1)
        let dateFin = new Date(dateFiniRef.current.value)
        dateFin.setDate(dateFin.getDate()+1)
        if (event.target.name === 'iniFecha') {
            setSelectedInitDate(dayjs.utc(dateIni).local().format('YYYY-MM-DD'))   
        }
        if (event.target.name === 'finFecha') {
            setSelectedFinDate(dayjs.utc(dateFin).local().format('YYYY-MM-DD'))
        }
        let response = await fetch(`${config.urlBase}ordenes/almacen/all?fechaIni=${dateIni}&fechaFin=${dateFin}`)
        response = await response.json()
        setOrders(response.data);
    }

    const handleChangeWarehouse = () => {
        setActiveWarehouse(warehouseRef.current.value)
    }

    return (
        <>
            <div>
            <Row>
                <Col>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-default">Fecha Inicial</InputGroup.Text>
                        <FormControl
                            type="date"
                            onChange={(e) => handleChangeDate(e)}
                            ref={dateIniRef}
                            value={selectedInitDate}
                            name="iniFecha"
                            max={selectedFinDate}
                        />
                    </InputGroup>
                </Col>
                <Col>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-default">Fecha Final</InputGroup.Text>
                        <FormControl
                            type="date"
                            onChange={(e) => handleChangeDate(e)}
                            ref={dateFiniRef}
                            value={selectedFinDate}
                            name="finFecha"
                            min={selectedInitDate}
                        />
                    </InputGroup>
                </Col>
            </Row>
            </div>
            {
                orders.length > 0 &&
                <Row>
                    <Col xs={3}>
                        <FloatingLabel controlId="floatingSelect" label="Almacen" className="mb-4 mt-4">
                            <Form.Select aria-label="Floating label select example" ref={warehouseRef} onChange={handleChangeWarehouse}>
                                {
                                    warehouses.map(warehouse => <option key={warehouse.almacen} value={warehouse.almacen}>{warehouse.descripcion}</option>)
                                }
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                </Row>
            }
            {
                orders.length === 0 && <h1>No se encontraron registros.</h1>
            }
            {
                orders.length > 0 && 
                (
                    <TableContainer>
                        <Table hover responsive striped bordered>
                            <thead>
                                <tr>
                                    {headers.map(header => (
                                        <th key={header.id}>{header.description}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {orders.map(order => {
                                    if(order.almacen === activeWarehouse) {
                                        return (
                                            <tr key={order.orden}>
                                                <td>
                                                    {order.orden}
                                                </td>
                                                <td>
                                                    {dayjs.utc(order.fecha).format('DD/MM/YYYY')}
                                                </td>
                                                <td>
                                                    {branches.map(branch => branch.sucursal === order.sucursal ? branch.descripcion : "")}
                                                </td>
                                                <td>
                                                    {order.paquetes}
                                                </td>
                                                <td>
                                                    {order.estatus}
                                                </td>
                                                <td>
                                                    {drivers.map(driver => driver.chofer === order.choferRecoleccion ? driver.nombre : "")}
                                                </td>
                                            </tr>
                                        )
                                        } else {
                                            return <></>
                                        }
                                    }
                                )}
                            </tbody>
                        </Table>
                    </TableContainer>
                )
            }
        </>
        )
};

export default TableInWarehouseOrder;