import { useContext, useRef } from 'react';
import { useHistory } from 'react-router';
import AppContext from '../context/AppContext';
import styled from 'styled-components';
import PersonIcon from '@material-ui/icons/Person';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import toast, { Toaster } from 'react-hot-toast';

import Logo from '../assets/logo_largo_completo_negro_sub.svg';

import config from '../config'

const Container = styled.div`
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
`;

const Badge = styled.img`
    padding: 10px;
    margin-top: 80px;
    width: 800px;
    @media (max-width: 820px) {
        width: 420px;
    }
    @media (max-width: 620px) {
        width: 320px;
    }
    @media (max-width: 420px) {
        width: 220px;
    }
    @media (max-width: 320px) {
        width: 170px;
    }
`;

const Input = styled.div`
    box-sizing: border-box;
    display: flex;
    align-items: center;
    width: 70%;
    height: 40px;
    margin-top: 15px;
    margin-bottom: 10px;
    border-bottom: 1px black solid;
    transition: 0.6s;

    &:focus-within {
        border-bottom: 3px black solid;
    }
`;

const Box = styled.input`
    flex-grow: 1;
    padding-left: 10px;
    background: none;
    border: none;
    outline: none;
`;

const BtnLog = styled.button`
    margin-top: 30px;
    background-color: transparent;
    width: 70%;
    height: 40px;
    border: solid 1px black;

    &:hover {
        cursor: pointer;
        background-color: black;
        color: white;
    }

`;

const Log = () => {
    const history = useHistory();
    const userRef = useRef(null);
    const passwordRef = useRef(null);
    const { signUser } = useContext(AppContext);

    const handleSignUser = async () => {
        let response = await fetch(`${config.urlBase}acceso/login`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                usuario: userRef.current.value,
                password: passwordRef.current.value
            })
        });
        response = await response.json();
        if(response.data) {
            signUser(response.data);
            history.push("/application")
        }else {
            toast.error(response.message);
        }
        
    };

    return (
        <Container>
            <Badge src={Logo} alt="Logo Monlog"/>
            <Input>
                <PersonIcon />
                <Box type="text" autoFocus ref={userRef}/>
            </Input>
            <Input>
                <VpnKeyIcon />
                <Box type="password" ref={passwordRef}/>
            </Input>
            <BtnLog onClick={handleSignUser}>
                iniciar sesion
            </BtnLog>
            <Toaster />
        </Container>
    )
};

export default Log;