import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc'
import { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Table, Modal, InputGroup, FormControl, Form } from 'react-bootstrap';

import config from '../config';

dayjs.extend(utc);

const TableContainer = styled.div`
    margin: auto;
    width: 780px;
    height: 300px;
    overflow-y: scroll;
    @media (max-width: 860px) {
        width: 780px;
    }
    @media (max-width: 760px) {
        width: 580px;
    }
    @media (max-width: 560px) {
        width: 380px;
    }
    @media (max-width: 360px) {
        width: 280px;
    }
`;

const DeleteButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: red;
    }
`;

const EditButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: #fdd448;
    }
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CatalogUser = () => {

    const [elements, setElements] = useState([]);
    const [selectElement, setSelectElement] = useState({});
    const [drivers, setDrivers] = useState([]);
    const [showAddModal, setShowAddModal] = useState(false);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const codigoAgregarRef = useRef(null);
    const descripcionAgregarRef = useRef(null);
    const passwordAgregarRef = useRef(null);
    const correoAgregarRef = useRef(null);
    const tipoAgregarRef = useRef(null);
    const choferRef = useRef(null);

    useEffect(()=> {
        async function fetchData(){
            let response = await fetch(`${config.urlBase}usuarios/`);
            let users = await response.json();
            setElements(users);
            let choferes = await fetch(`${config.urlBase}choferes/`);
            choferes = await choferes.json();
            setDrivers(choferes);
        }
        fetchData()
    },[]);

    const handleCloseAddModal = () => setShowAddModal(false);
    const handleShowAddModal = () => setShowAddModal(true);
    const handleCloseEditModal = () => setShowEditModal(false);
    const handleShowEditModal = (element) => {
        setSelectElement(element);
        setShowEditModal(true);
    };
    const handleCloseDeleteModal = () => setShowDeleteModal(false);
    const handleShowDeleteModal = (element) => {
        setSelectElement(element);
        setShowDeleteModal(true);
    };
    

    const handleDelete = async () => {
        let response = await fetch(`${config.urlBase}usuarios/eliminar?usuario=${selectElement.usuario}`,{
            method: 'DELETE'
        });
        let data = await response.json();
        console.log(data);
        setElements(elements.filter(element => element.usuario !== selectElement.usuario));
        setShowDeleteModal(false);
    };

    const handleAdd = async () => {
        let newElement = {};
        newElement.usuario = codigoAgregarRef.current.value;
        newElement.nombre = descripcionAgregarRef.current.value;
        newElement.password = passwordAgregarRef.current.value;
        newElement.correo = correoAgregarRef.current.value;
        newElement.tipo = tipoAgregarRef.current.value;
        let response = await fetch(`${config.urlBase}usuarios/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        setElements(elements.concat(response.data))
        setShowAddModal(false);
    };

    const handleEditChange = (event) => {
        setSelectElement({
            ...selectElement,
            [event.target.name]: event.target.value
        });
    };

    const handleEdit = async () => {
        let newElement = {};
        newElement.usuario = selectElement.usuario;
        newElement.nombre = selectElement.nombre;
        newElement.password = selectElement.password;
        newElement.correo = selectElement.correo;
        newElement.tipo = selectElement.tipo;
        let response = await fetch(`${config.urlBase}usuarios/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        let newsElements = elements;
        newsElements.map( element => {
            if (element.usuario === selectElement.usuario) {
                element.usuario = response.data.usuario;
                element.nombre = response.data.nombre;
                element.password = response.data.password;
                element.correo = response.data.correo;
                element.tipo = response.data.tipo;
                element.activo = response.data.activo;
                element.dentro = response.data.dentro;
                element.ultimoAcceso = response.data.ultimoAcceso;
            }

            return element
        })
        setElements(newsElements)
        setShowEditModal(false);
    };

    return (
        <>
            <CustomButton onClick={handleShowAddModal}>AGREGAR</CustomButton>
            <Modal
                show={showAddModal}
                onHide={handleCloseAddModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>AGREGAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Usuario</InputGroup.Text>
                        <FormControl aria-label="Codigo" aria-describedby="inputGroup-sizing-sm" ref={codigoAgregarRef} maxLength="10"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Nombre</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={descripcionAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Contrasea</InputGroup.Text>
                        <FormControl aria-label="Password" type="password" aria-describedby="inputGroup-sizing-sm" ref={passwordAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Correo</InputGroup.Text>
                        <FormControl aria-label="Correo" aria-describedby="inputGroup-sizing-sm" ref={correoAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Tipo</InputGroup.Text>
                        <Form.Select aria-label="Tipo" ref={tipoAgregarRef} >
                            <option value="1">Administrador</option>
                            <option value="2">Chofer</option>
                        </Form.Select>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Chofer</InputGroup.Text>
                        <Form.Select aria-label="Tipo" ref={choferRef} >
                            <option value="">...</option>
                            {
                                drivers.map(driver => <option value={driver.chofer}>{driver.nombre}</option>)
                            }
                        </Form.Select>
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleAdd} >AGREGAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseAddModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showEditModal}
                onHide={handleCloseEditModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>EDITAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Usuario</InputGroup.Text>
                        <FormControl 
                            disabled 
                            aria-label="Usuario" 
                            aria-describedby="inputGroup-sizing-sm" 
                            value={selectElement.usuario}
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Nombre</InputGroup.Text>
                        <FormControl 
                            aria-label="Nombre" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="100" 
                            value={selectElement.nombre}
                            onChange={handleEditChange}
                            name="nombre"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Contrasea</InputGroup.Text>
                        <FormControl 
                            aria-label="Password" 
                            type="password" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={""}
                            onChange={handleEditChange}
                            name="password"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Correo</InputGroup.Text>
                        <FormControl 
                            aria-label="Correo" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.correo}
                            onChange={handleEditChange}
                            name="correo"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Tipo</InputGroup.Text>
                        <Form.Select 
                            aria-label="Tipo"
                            value={selectElement.tipo}
                            onChange={handleEditChange}
                            name="tipo"
                        >
                            <option value="1">Administrador</option>
                            <option value="2">Chofer</option>
                        </Form.Select>
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleEdit} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseEditModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showDeleteModal}
                onHide={handleCloseDeleteModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>CONFIRMACION NECESARIA!</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {`Al precionar aceptar, el registro ${selectElement.usuario} ser elminado permanentemente, desea continuar?`}
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleDelete} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseDeleteModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <TableContainer>
                <h3>USUARIOS</h3>
                <Table striped hover responsive>
                    <thead>
                        <tr>
                            <th>Usuario</th>
                            <th>Nombre</th>
                            <th>Tipo</th>
                            <th>Ultimo Acceso</th>
                            <th>{" "}</th>
                            <th>{" "}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            elements.length > 0 && elements.map( usuario => (
                                <tr key={usuario.usuario}>
                                    <td>
                                        {usuario.usuario}
                                    </td>
                                    <td>{usuario.nombre}</td>
                                    <td>{usuario.tipo === 1 ? 'Administrador' : 'Chofer'}</td>
                                    <td>{usuario.ultimoAcceso ? dayjs.utc(usuario.ultimoAcceso).format('DD/MM/YY') : " "}</td>
                                    <td>
                                        <EditButton onClick={()=>handleShowEditModal(usuario)}>
                                            <EditIcon />
                                        </EditButton>
                                    </td>
                                    <td>
                                        <DeleteButton onClick={()=>handleShowDeleteModal(usuario)}>
                                            <DeleteIcon />
                                        </DeleteButton>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </Table>
                {
                    elements.length === 0 && <h1>No hay datos por mostrar</h1>
                }
            </TableContainer>
        </>
    )
};

export default CatalogUser;