import { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Table, Modal,InputGroup, FormControl } from 'react-bootstrap';

import config from '../config';

const TableContainer = styled.div`
    margin: auto;
    width: 780px;
    height: 300px;
    overflow-y: scroll;
    @media (max-width: 860px) {
        width: 780px;
    }
    @media (max-width: 760px) {
        width: 580px;
    }
    @media (max-width: 560px) {
        width: 380px;
    }
    @media (max-width: 360px) {
        width: 280px;
    }
`;

const DeleteButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: red;
    }
`;

const EditButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: #fdd448;
    }
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CatalogCustomer = () => {
    const [elements, setElements] = useState([]);
    const [selectElement, setSelectElement] = useState({});
    const [showAddModal, setShowAddModal] = useState(false);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const codigoAgregarRef = useRef(null);
    const descripcionAgregarRef = useRef(null);
    const direccionAgregarRef = useRef(null);
    const numeroExtAgregarRef = useRef(null);
    const numeroIntAgregarRef = useRef(null);
    const coloniaAgregarRef = useRef(null);
    const ciudadAgregarRef = useRef(null);
    const estadoAgregarRef = useRef(null);
    const codigoPostalAgregarRef = useRef(null);
    const rfcAgregarRef = useRef(null);
    const contactoAgregarRef = useRef(null);
    const conTelefonoAgregarRef = useRef(null);
    const conEmailAgregarRef = useRef(null);
    const nombreCortoAgregarRef = useRef(null);

    useEffect(()=> {
        async function fetchData(){
            let response = await fetch(`${config.urlBase}clientes/`);
            let customers = await response.json();
            setElements(customers)
        }
        fetchData()
    },[]);

    const handleCloseAddModal = () => setShowAddModal(false);
    const handleShowAddModal = () => setShowAddModal(true);
    const handleCloseEditModal = () => setShowEditModal(false);
    const handleShowEditModal = (element) => {
        setSelectElement(element);
        setShowEditModal(true);
    };
    const handleCloseDeleteModal = () => setShowDeleteModal(false);
    const handleShowDeleteModal = (element) => {
        setSelectElement(element);
        setShowDeleteModal(true);
    };
    

    const handleDelete = async () => {
        let response = await fetch(`${config.urlBase}clientes/eliminar?cliente=${selectElement.cliente}`,{
            method: 'DELETE'
        });
        let data = await response.json();
        console.log(data);
        setElements(elements.filter(element => element.cliente !== selectElement.cliente));
        setShowDeleteModal(false);
    };

    const handleAdd = async () => {
        let newElement = {};
        newElement.cliente = codigoAgregarRef.current.value;
        newElement.razonSocial = descripcionAgregarRef.current.value;
        newElement.direccion = direccionAgregarRef.current.value;
        newElement.numeroExt = numeroExtAgregarRef.current.value;
        newElement.numeroInt = numeroIntAgregarRef.current.value;
        newElement.colonia = coloniaAgregarRef.current.value;
        newElement.ciudad = ciudadAgregarRef.current.value;
        newElement.estado = estadoAgregarRef.current.value;
        newElement.codigoPostal = codigoPostalAgregarRef.current.value;
        newElement.rfc = rfcAgregarRef.current.value;
        newElement.contacto = contactoAgregarRef.current.value;
        newElement.conTelefono = conTelefonoAgregarRef.current.value;
        newElement.conEmail = conEmailAgregarRef.current.value;
        newElement.nombreCorto = nombreCortoAgregarRef.current.value;
        let response = await fetch(`${config.urlBase}clientes/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        setElements(elements.concat(response.data))
        setShowAddModal(false);
    };

    const handleEditChange = (event) => {
        setSelectElement({
            ...selectElement,
            [event.target.name]: event.target.value
        });
    };

    const handleEdit = async () => {
        let newElement = {};
        newElement.cliente = selectElement.cliente;
        newElement.razonSocial = selectElement.razonSocial;
        newElement.direccion = selectElement.direccion;
        newElement.numeroExt = selectElement.numeroExt;
        newElement.numeroInt = selectElement.numeroInt;
        newElement.colonia = selectElement.colonia;
        newElement.ciudad = selectElement.ciudad;
        newElement.estado = selectElement.estado;
        newElement.codigoPostal = selectElement.codigoPostal;
        newElement.rfc = selectElement.rfc;
        newElement.contacto = selectElement.contacto;
        newElement.conTelefono = selectElement.conTelefono;
        newElement.conEmail = selectElement.conEmail;
        newElement.nombreCorto = selectElement.nombreCorto;
        let response = await fetch(`${config.urlBase}clientes/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        let newsElements = elements;
        newsElements.map( element => {
            if (element.cliente === selectElement.cliente) {
                element.cliente = response.data.cliente;
                element.razonSocial = response.data.razonSocial;
                element.direccion = response.data.direccion;
                element.numeroExt = response.data.numeroExt;
                element.numeroInt = response.data.numeroInt;
                element.colonia = response.data.colonia;
                element.ciudad = response.data.ciudad;
                element.estado = response.data.estado;
                element.codigoPostal = response.data.codigoPostal;
                element.rfc = response.data.rfc;
                element.contacto = response.data.contacto;
                element.conTelefono = response.data.conTelefono;
                element.conEmail = response.data.conEmail;
                element.activo = response.data.activo;
            }

            return element
        })
        setElements(newsElements)
        setShowEditModal(false);
    };

    return (
        <>
            <CustomButton onClick={handleShowAddModal}>AGREGAR</CustomButton>
            <Modal
                show={showAddModal}
                onHide={handleCloseAddModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>AGREGAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo</InputGroup.Text>
                        <FormControl aria-label="Codigo" aria-describedby="inputGroup-sizing-sm" ref={codigoAgregarRef} maxLength="10"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Razon Social</InputGroup.Text>
                        <FormControl aria-label="RazonSocial" aria-describedby="inputGroup-sizing-sm" ref={descripcionAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Nombre corto</InputGroup.Text>
                        <FormControl aria-label="nombreCorto" aria-describedby="inputGroup-sizing-sm" ref={nombreCortoAgregarRef} maxLength="50"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Direccion</InputGroup.Text>
                        <FormControl aria-label="Direccion" aria-describedby="inputGroup-sizing-sm" ref={direccionAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Exterior</InputGroup.Text>
                        <FormControl aria-label="NumeroExterior" aria-describedby="inputGroup-sizing-sm" ref={numeroExtAgregarRef} maxLength="50"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Interior</InputGroup.Text>
                        <FormControl aria-label="NumeroInterior" aria-describedby="inputGroup-sizing-sm" ref={numeroIntAgregarRef} maxLength="50"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Colonia</InputGroup.Text>
                        <FormControl aria-label="Colonia" aria-describedby="inputGroup-sizing-sm" ref={coloniaAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Ciudad</InputGroup.Text>
                        <FormControl aria-label="Ciudad" aria-describedby="inputGroup-sizing-sm" ref={ciudadAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Estado</InputGroup.Text>
                        <FormControl aria-label="Estado" aria-describedby="inputGroup-sizing-sm" ref={estadoAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo Postal</InputGroup.Text>
                        <FormControl aria-label="CodigoPostal" aria-describedby="inputGroup-sizing-sm" ref={codigoPostalAgregarRef} maxLength="30"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">R.F.C.</InputGroup.Text>
                        <FormControl aria-label="RFC" aria-describedby="inputGroup-sizing-sm" ref={rfcAgregarRef} maxLength="30"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Contacto</InputGroup.Text>
                        <FormControl aria-label="Contacto" aria-describedby="inputGroup-sizing-sm" ref={contactoAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Telefono</InputGroup.Text>
                        <FormControl aria-label="TelefonoDeContacto" aria-describedby="inputGroup-sizing-sm" ref={conTelefonoAgregarRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Correo electronico</InputGroup.Text>
                        <FormControl aria-label="EmailDeContacto" aria-describedby="inputGroup-sizing-sm" ref={conEmailAgregarRef} maxLength="100"/>
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleAdd} >AGREGAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseAddModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showEditModal}
                onHide={handleCloseEditModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>EDITAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo</InputGroup.Text>
                        <FormControl 
                            disabled 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            value={selectElement.cliente}
                            name="cliente"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Razon Social</InputGroup.Text>
                        <FormControl 
                            aria-label="RazonSocial" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="100" 
                            value={selectElement.razonSocial}
                            onChange={handleEditChange}
                            name="razonSocial"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Nombre corto</InputGroup.Text>
                        <FormControl 
                            aria-label="nombreCorto" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="50" 
                            value={selectElement.nombreCorto}
                            onChange={handleEditChange}
                            name="nombreCorto"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Direccion</InputGroup.Text>
                        <FormControl 
                            aria-label="Direccion" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="100"
                            value={selectElement.direccion}
                            onChange={handleEditChange}
                            name="direccion"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Exterior</InputGroup.Text>
                        <FormControl 
                            aria-label="NumeroExterior" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="50"
                            value={selectElement.numeroExt}
                            onChange={handleEditChange}
                            name="numeroExt"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Interior</InputGroup.Text>
                        <FormControl 
                            aria-label="NumeroInterior" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="50"
                            value={selectElement.numeroInt}
                            onChange={handleEditChange}
                            name="numeroInt"    
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Colonia</InputGroup.Text>
                        <FormControl 
                            aria-label="Colonia" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.colonia}
                            onChange={handleEditChange}
                            name="colonia"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Ciudad</InputGroup.Text>
                        <FormControl 
                            aria-label="Ciudad" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.ciudad}
                            onChange={handleEditChange}
                            name="ciudad"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Estado</InputGroup.Text>
                        <FormControl 
                            aria-label="Estado" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.estado}
                            onChange={handleEditChange}
                            name="estado"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo Postal</InputGroup.Text>
                        <FormControl 
                            aria-label="CodigoPostal" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="30"
                            value={selectElement.codigoPostal}
                            onChange={handleEditChange}
                            name="codigoPostal"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">R.F.C.</InputGroup.Text>
                        <FormControl 
                            aria-label="RFC" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="30"
                            value={selectElement.rfc}
                            onChange={handleEditChange}
                            name="rfc"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Contacto</InputGroup.Text>
                        <FormControl 
                            aria-label="Contacto"
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.contacto}
                            onChange={handleEditChange}
                            name="contacto"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Telefono</InputGroup.Text>
                        <FormControl 
                            aria-label="TelefonoDeContacto" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.conTelefono}
                            onChange={handleEditChange}
                            name="conTelefono"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Correo electronico</InputGroup.Text>
                        <FormControl 
                            aria-label="EmailDeContacto" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.conEmail}
                            onChange={handleEditChange}
                            name="conEmail"
                        />
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleEdit} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseEditModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showDeleteModal}
                onHide={handleCloseDeleteModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>CONFIRMACION NECESARIA!</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {`Al precionar aceptar, el registro ${selectElement.razonSocial} ser elminado permanentemente, desea continuar?`}
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleDelete} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseDeleteModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <TableContainer>
                <h3>CLIENTES</h3>
                <Table striped hover responsive>
                    <thead>
                        <tr>
                        <th>Codigo</th>
                        <th>Razon Social</th>
                        <th>{" "}</th>
                        <th>{" "}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            elements.length > 0 && elements.map( cliente => (
                                <tr key={cliente.cliente}>
                                    <td>
                                        {cliente.cliente}
                                    </td>
                                    <td>{cliente.razonSocial}</td>
                                    <td>
                                        <EditButton onClick={()=>handleShowEditModal(cliente)}>
                                            <EditIcon />
                                        </EditButton>
                                    </td>
                                    <td>
                                        <DeleteButton onClick={()=>handleShowDeleteModal(cliente)}>
                                            <DeleteIcon />
                                        </DeleteButton>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </Table>
                {
                    elements.length === 0 && <h1>No hay datos por mostrar</h1>
                }
            </TableContainer>
        </>
    )
};

export default CatalogCustomer;