import dayjs from 'dayjs';
import styled from "styled-components";
import { useEffect, useState, useReducer, useRef } from 'react';
import { Table, Modal, Form } from 'react-bootstrap';
import PersonAddDisabledIcon from '@material-ui/icons/PersonAddDisabled';

import config from '../config';

const headers = [
    {id: 1, description: 'fecha'},
    {id: 2, description: 'guia'},
    {id: 3, description: 'chofer'},
    {id: 4, description: 'estatus'}
];

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
`;

const BoxTwo = styled.div`
    width: 100%;
    margin-bottom: 25px;
`;

const BtnAssign = styled.button`
    display: flex;
    width: 200px;
    justify-content: space-around;
    align-items: center;
    background-color: #fdd448;
    color: white;
    border: none;
    padding: 5px;

    &:active {
        background-color: #d6b43e;
    }

    @media (max-width: 500px) {
        width: 300px;
        margin: 0 auto;
    }
    @media (max-width: 320px) {
        width: 270px;
        margin: 0 auto;
    }
`;

const TextBtn = styled.p`
    margin: 0;
`;

const Display = styled.div`
    border: solid 1.5px black;
    height: 100px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const initialState = {
    markedOrders: 0,
    selectedOrders: []
}

function reducer(state, action) {
    switch (action.type) {
        case "add_one":
            return {
                markedOrders: state.markedOrders+1,
                selectedOrders: state.selectedOrders.concat(action.payload)
            }
        case "substract_one":
            return {
                markedOrders: state.markedOrders-1,
                selectedOrders: state.selectedOrders.filter(selectedOrder => selectedOrder.orden !== action.payload.orden)
            }
        case "add_all":
            return {
                markedOrders: action.payload.length,
                selectedOrders: action.payload
            }
        case "substract_all":
            return {
                markedOrders: 0,
                selectedOrders: []
            }
        default:
            throw new Error();
    }
}

const GuideInTransit = () => {
    const [guides, setGuides] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const [state, dispach] = useReducer(reducer,initialState);
    const [assignShow, setAssignShow] = useState(false);
    const [almacenes,setAlmacenes] = useState([]);
    const almacenRef = useRef(null);

    //TODO: Que solo vengan guias ligadas a almacen ligado a usuario.
  
    useEffect(() => {
        async function fetchOrders() {
            let response = await fetch(`${config.urlBase}guias/consulta/guias/entransito`);
            response = await response.json();
            setGuides(response.data);
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            responseW = await responseW.json();
            setAlmacenes(responseW);
            let responseD = await fetch(`${config.urlBase}choferes/`);
            responseD = await responseD.json();
            setDrivers(responseD);
        }

        fetchOrders()
    },[]);

    const handleAssignClose = () => setAssignShow(false);
    const handleAssignShow = () => setAssignShow(true);

    const handleChange = (event, order) => {
        if (event.target.checked) {
            dispach({type: "add_one", payload: order})
        } else {
            dispach({type: "substract_one", payload: order})
        }
    };
    
    const handleAllChange = (event, orders) => {
        if (event.target.checked) {
            dispach({type: "add_all", payload: orders})
        } else {
            dispach({type: "substract_all"})
        }
    };

    const handlingUnassignment = async () => {
        let promises = state.selectedOrders.map(sO => fetch(`${config.urlBase}guias/desasignar`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                guia: sO.guia
            })
        }))
        let responses = await Promise.all(promises);
        console.log(responses)
        let newOrders = guides;
        dispach({type: "substract_all"})
        state.selectedOrders.map(selectedOrder => newOrders = newOrders.filter(order => order.guia !== selectedOrder.guia))
        setGuides(newOrders);
    }

    return (
        <>
            {state.markedOrders > 0 &&
                <BoxTwo>
                    <BtnAssign onClick={handleAssignShow}>
                        <PersonAddDisabledIcon />
                        <TextBtn>DESASIGNAR</TextBtn>
                    </BtnAssign>
                    <Modal show={assignShow} onHide={handleAssignClose} backdrop="static">
                        <Modal.Header closeButton>
                        <Modal.Title>DESHACER ASIGNACION DE CHOFER</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3" >
                                    <Form.Label>Ordenes que seran marcadas como nuevas:</Form.Label>
                                    <Display>
                                        <ul>
                                            {state.selectedOrders.map(selectedOrder => <li key={selectedOrder.guia}>{selectedOrder.guia}</li>)}
                                        </ul>
                                    </Display>
                                </Form.Group>
                            </Form>
                        </Modal.Body>
                        <Modal.Footer>
                        <CustomButtonOutline onClick={handleAssignClose}>
                            CERRAR
                        </CustomButtonOutline>
                        <CustomButton onClick={handlingUnassignment}>
                            DESASIGNAR
                        </CustomButton>
                        </Modal.Footer>
                    </Modal>
                </BoxTwo>
            }
            {
                guides.length === 0 && <h1>No se encontraron registros.</h1>
            }
            {
                guides.length > 0 && 
                (
                    <TableContainer>
                        <Table hover responsive striped bordered>
                            <thead>
                                <tr>
                                    <th>
                                    {state.markedOrders > 0 && <small style={{marginRight: 10 + 'px'}}>{state.markedOrders}</small>}
                                        <input type="checkbox" onChange={(event) => handleAllChange(event,guides)} />
                                    </th>
                                    {headers.map(header => (
                                        <th key={header.id}>{header.description}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {guides.map(guide => (
                                    <tr key={guide.guia}>
                                        <td>
                                            <input type="checkbox" checked={state.selectedOrders.includes(guide)} onChange={(event) => handleChange(event, guide)} name={guide.guia}/>
                                        </td>
                                        <td>
                                            {dayjs.utc(guide.FechaEntregaChofer).format('DD/MM/YYYY HH:mm a')}
                                        </td>
                                        <td>
                                            {guide.guia}
                                        </td>
                                        <td>
                                            {drivers.map(driver => guide.choferEntrega === driver.chofer ? driver.nombre : "") }
                                        </td>
                                        <td>
                                            {guide.estatus}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                    </TableContainer>
                )
            }
        </>
        )
};

export default GuideInTransit;