import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';

import Catalogs from '../containers/Catalogs';
import Guides from '../containers/Guides';
import Log from '../containers/Log';
import Orders from '../containers/Orders';
import DriverOrders from '../containers/DriverOrders';
import DriverGuides from '../containers/DriverGuides'
import AppContext from '../context/AppContext';
import useInitialState from '../hooks/useInitialState';
import Reports from '../containers/Reports';
import DriverReturns from '../containers/DriverReturns';
import ApplicationFull from '../containers/ApplicationFull';

const App = () => {
    const initialState = useInitialState();

    const MyRoute = (props) => {
        return (
            initialState.state.signedUser ? 
               <Route {...props} />
               :
               <Redirect to="/"/> 
        )
    }

    return (
        <AppContext.Provider value={initialState}>
            <BrowserRouter>
                <Switch>
                    <Route exact path="/" component={Log} />
                    <MyRoute exact path="/orders" component={initialState.state.user.tipo === 1 ? Orders : DriverOrders} />
                    <MyRoute exact path="/catalogs" component={Catalogs} />
                    <MyRoute exact path="/reports" component={Reports} />
                    <MyRoute exact path="/guides" component={initialState.state.user.tipo === 1 ? Guides : DriverGuides} />
                    <MyRoute exact path="/returns" component={DriverReturns} />
                    <MyRoute exact path="/application" component={initialState.state.user.tipo === 1 ? ApplicationFull : DriverOrders} />
                    {/*<Route component={} /> */}
                </Switch>
            </BrowserRouter>
        </AppContext.Provider>
    )
};

export default App;