import { useContext } from 'react';
import styled from 'styled-components';
import AppContext from '../context/AppContext';

import Logo from '../assets/logo_largo_negro.svg';

import MenuOpenIcon from '@material-ui/icons/MenuOpen';
import { Link } from 'react-router-dom';

const Container = styled.div`
    position: relative;
    z-index: 15;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fdd448;
    width: 30%;
    left: ${props => !props.visibleMenu ? '-30%' : '0'};
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    transition: 0.3s;
    @media (max-width: 820px) {
        width: 420px;
        left: ${props => !props.visibleMenu ? '-420px' : '0'};
    }
    @media (max-width: 620px) {
        width: 320px;
        left: ${props => !props.visibleMenu ? '-320px' : '0'};
    }
    @media (max-width: 420px) {
        width: 220px;
        left: ${props => !props.visibleMenu ? '-220px' : '0'};
    }
    @media (max-width: 320px) {
        width: 170px;
        left: ${props => !props.visibleMenu ? '-170px' : '0'};
    }
`;

const BtnMenu = styled.button`
    width: 100%;
    height: 55px;
    text-align: right;
    background-color: black;
    color: white;
    border: none;
    margin-bottom: 50px;
`;

const ImgMenu = styled.img`
    height: 60px;
    margin-bottom: 50px;

    @media (max-width: 820px) {
        height: 60px;
    }
    @media (max-width: 620px) {
        height: 60px;
    }
    @media (max-width: 420px) {
        height: 40px;
    }
    @media (max-width: 320px) {
        height: 40px;
    }
`

const Item = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    color: black;
    font-weight: bold;
    width: 100%;
    height: 55px;
    cursor: pointer;
    
    &:hover {
        background-color: black;
        color: white;
    }

    &::active {
        background-color: black;
        color: white;
    }
`;

const ItemText = styled.p`
    margin: 0;
    text-align: center;
`;

const AppMenu = () => {
    const { state: { visibleMenu, user }, toggleMenu } = useContext(AppContext);
    
    const handleClick = () => toggleMenu(visibleMenu);

    const options = [
        {id: 1, tittle:'ORDENES DE RECOLECCION', path: "/orders", access: [1,2]},
        {id: 2, tittle:'GUIAS DE ENTREGA', path: "/guides", access: [1,2]},
        {id: 3, tittle:'CATALOGOS',path: "/catalogs", access: [1]},
        {id: 4, tittle:'REPORTES',path: "/reports", access: [1]},
        {id: 5, tittle:'GUIAS DEVOLUCION',path: "/returns", access: [2]}
    ];
    
    return (
        <Container visibleMenu={visibleMenu}>
            <BtnMenu onClick={handleClick}>
                <MenuOpenIcon />
            </BtnMenu>
            <ImgMenu src={Logo} alt="Logo Monlog"/>
            {options.map( option =>
               ( option.access.includes(user.tipo) ?
                    <Item key={option.id}>
                        <Link to={option.path} style={{ textDecoration: 'none', color: "inherit"}} onClick={handleClick}>
                            <ItemText>
                                {option.tittle}
                            </ItemText>
                        </Link>
                    </Item>
                :
                <></>)
            )}
        </Container>
    )
};

export default AppMenu;