import QrScan from 'react-qr-reader'
import { useState, useContext, useEffect, useRef } from 'react';
import dayjs from 'dayjs';
import styled from 'styled-components';
import { Table, Modal, InputGroup, FormControl, Row, Col, Alert, Form } from 'react-bootstrap';
import toast, { Toaster } from 'react-hot-toast';
import AppContext from '../context/AppContext';
import Layout from "../components/Layout";
import SignatureCanvas from 'react-signature-canvas'

import config from '../config';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const TextModal = styled.p`
    margin-bottom: 5px;
`;

const ScanCodeButton = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    border: none;
    color: white;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 10px;
    padding: 10px;
`;

const ImgGContainer = styled.img`
    display: ${props => props.visible ? 'block' : 'none'};
`;

const opciones = {};
opciones["ENTRADA"] = <h1>GUIA</h1>

const DriverGuides = () => {
    const { state: { user } } = useContext(AppContext);
    const [guides,setGuides] = useState([]);
    const [selectedElement, setSelectedElement] = useState({});
    const [show, setShow] = useState(false);
    const [showFotoCasa, setShowFotoCasa] = useState(false);
    const [showFotoIfe, setShowFotoIfe] = useState(false);
    const [showFotoOtro, setShowFotoOtro] = useState(false);
    const quienRecibeRef = useRef(null);
    const fotoCasaRef = useRef(null);
    const fotoIfeRef = useRef(null);
    const fotoOtroRef = useRef(null);
    const [body, setBody] = useState({});
    const motivoRef = useRef(null);
    const [showModalReturn, setShowModalReturn] = useState(false);
    const [showModalVerify, setShowModalVerify] = useState(false);
    const [showMessage, setShowMessage] = useState(false);
    const [badGuideMessage, setBadGuideMessage] = useState(false);

    useEffect(() => {
        async function fetchData() {
            console.log(dayjs.utc().local().format('YYYY-MM-DD'))
            let responseG = await fetch(`${config.urlBase}guias/consulta/guias/chofer?chofer=${user.chofer}&fecha=${dayjs.utc().local().format('YYYY-MM-DD')}`);
            responseG = await responseG.json();
            setGuides(responseG.data);
        }
        fetchData()
    },[user.chofer]);

    const handleChange = (e) => {
        let reader = new FileReader();
        reader.onload = event => {
            setBody({
                ...body,
                [`foto${e.target.name}`]:event.target.result.replace(/^data:image\/[a-z]+;base64,/, "")
            });
            if(e.target.name === "Ife") {
                fotoIfeRef.current.src = event.target.result
                setShowFotoIfe(true);
            }
            if(e.target.name === "Casa") {
                fotoCasaRef.current.src = event.target.result
                setShowFotoCasa(true);
            }
            if(e.target.name === "Otro") {
                fotoOtroRef.current.src = event.target.result
                setShowFotoOtro(true);
            }
        }
        reader.readAsDataURL(e.target.files[0])
    }

    const handleOrderRegistration = async () => {
        //TODO: VALIDAR QUE LOS CAMPOS NO ESTEN EN BLANCO
        try {
            if (!quienRecibeRef.current.value) {
                throw Error('Se debe indicar quien recibe el paquete')
            }
            let newBody = {
                ...body,
                orden: selectedElement.orden,
                guia: selectedElement.guia,
                fecha: dayjs.utc().local().format('YYYY-MM-DDTHH:mm:ss'),
                recibePersona: quienRecibeRef.current.value
            }
            let responseRG = await fetch(`${config.urlBase}guias/entregada`,{
                method: 'POST',
                mode: 'cors',
                headers: {
                'Content-Type': 'application/json'
                },
                body: JSON.stringify(newBody)
            });
            responseRG = await responseRG.json();
            setGuides(guides.filter(guide => guide.guia !== selectedElement.guia));
            handleClose();
            toast.success(responseRG.message);
        } catch (error) {
            toast.error(error.message)
        }
    }

    const handleClose = () => {
        setBadGuideMessage(false);
        setShowFotoCasa(false);
        setShowFotoIfe(false);
        setShowFotoOtro(false);
        setShowMessage(false);
        setShow(false);
    }

    const handleShow = () => {
        setShow(true);
    }

    const handleCloseModalReturn = () => {
        setShowMessage(false);
        setBadGuideMessage(false);
        setShowModalReturn(false);
    }

    const handleShowModalReturn = async () => {
        setShowModalReturn(true);
    }

    const handleShowModalVerify = (element) => {
        setSelectedElement(element);
        setShowModalVerify(true)
    }

    const handleCloseModalVerify = () => {
        setShowMessage(false);
        setBadGuideMessage(false);
        setShowModalVerify(false);
    }

    const handleScan = data => {
        if (data) {
            if(data === selectedElement.guia) {
                setShowModalVerify(false);
                handleShow();
            } else {
                setBadGuideMessage(true);
            }
        }
    }

    const handleError = err => {
        console.error(err)
    }

    const handleVerify = () => {
        setBadGuideMessage(false);
        setShowMessage(true);
    }

    const handleReturn = async () => {
        try {
            if(motivoRef.current.value.length === 0){
                throw new Error('El motivo debe ser especificado')
            }
            let response = await fetch(`${config.urlBase}guias/devolucion`,{
                method: 'POST',
                mode: 'cors',
                headers: {
                'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    guia: selectedElement.guia,
                    fecha: dayjs.utc().local().format('YYYY-MM-DDTHH:mm:ss'),
                    motivo: motivoRef.current.value
                })
            });
            response = await response.json();
            setGuides(guides.filter(guide => guide.guia !== selectedElement.guia));
            handleClose();
            handleCloseModalReturn();
            toast.success(response.message)   
        } catch (error) {
            toast.error(error.message) 
        }
    }

    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>GUIAS DE ENTREGA</Tittle>
                </Box>
                {
                    guides.length > 0 &&
                    <h6 className="mb-3">TOTAL DE GUIAS: {guides.length}</h6>
                }
                {
                    guides.length > 0 && (
                        <TableContainer>
                            <Table hover responsive striped bordered>
                                <thead>
                                    <tr>
                                        <th>nombre</th>
                                        <th>direccion</th>
                                        <th>telofono</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {guides.map(guide => (
                                        <tr key={guide.guia} onClick={() => handleShowModalVerify(guide)}>
                                            <td>
                                                {guide.nombreClienteFinal}
                                            </td>
                                            <td>
                                                {`${guide.direccion} ${guide.numeroExt}, Col. ${guide.colonia}, ${guide.ciudad}, ${guide.estado}. ${guide.codigoPostal}`}
                                            </td>
                                            <td>
                                                {guide.telefono}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </Table>
                        </TableContainer>
                    )
                }
                {
                    guides.length === 0 && <h5>No cuenta con guias asignadas</h5>
                }
                <Modal
                    show={show}
                    onHide={handleClose}
                    backdrop="static"
                    keyboard={false}
                    fullscreen
                >
                    <Modal.Header closeButton>
                    <Modal.Title>DETALLE DE GUIA</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <TextModal><strong>GUIA:</strong> {selectedElement.guia}</TextModal>
                        <TextModal><strong>CLIENTE:</strong> {selectedElement.nombreClienteFinal}</TextModal>
                        <InputGroup className="mb-3">
                            <InputGroup.Text id="inputGroup-sizing-default">Quien recibe</InputGroup.Text>
                            <FormControl
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                ref={quienRecibeRef}
                            />
                        </InputGroup>
                        <Row className="g-2">
                            <Col md>
                                <Form.Group>
                                    <Form.Label>FOTO INE</Form.Label>
                                    <Form.Control type="file" accept="image/*" capture="camera" onChange={handleChange} name="Ife"/>
                                </Form.Group>
                            </Col>
                            <ImgGContainer alt="foto Ife" ref={fotoIfeRef} visible={showFotoIfe}/>
                            <Col md>
                                <Form.Group>
                                    <Form.Label>
                                        FOTO CASA
                                    </Form.Label>
                                    <Form.Control type="file" accept="image/*" capture="camera" onChange={handleChange} name="Casa"/>
                                </Form.Group>
                            </Col>
                            <ImgGContainer alt="foto casa" ref={fotoCasaRef} visible={showFotoCasa}/>
                            <Col md>
                                <Form.Group>
                                    <Form.Label>
                                        FOTO
                                    </Form.Label>
                                    <Form.Control type="file" accept="image/*" capture="camera" onChange={handleChange} name="Otro"/>
                                </Form.Group>
                            </Col>
                            <ImgGContainer alt="foto casa" ref={fotoOtroRef} visible={showFotoOtro}/>
                        </Row>
                        <p></p>
                        <CustomButtonOutline variant="secondary" onClick={handleShowModalReturn}>
                            MARCAR COMO DEVOLUCION 
                        </CustomButtonOutline>
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButton variant="primary" onClick={handleOrderRegistration}>
                        ENTREGADA
                    </CustomButton>
                    <CustomButtonOutline variant="secondary" onClick={handleClose}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={showModalReturn}
                    onHide={handleCloseModalReturn}
                    backdrop="static"
                    keyboard={false}
                    fullscreen
                >
                    <Modal.Header closeButton>
                    <Modal.Title>DEVOLUCION</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <TextModal><strong>GUIA:</strong>{' '+selectedElement.guia}</TextModal>
                        <TextModal><strong>CLIENTE:</strong>{' '+selectedElement.nombreClienteFinal}</TextModal>
                        <TextModal><strong>MOTIVO:</strong></TextModal>
                        <InputGroup className="mb-3">
                            <FormControl
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                ref={motivoRef}
                            />
                        </InputGroup>
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButton variant="primary" onClick={handleReturn}>
                        DEVOLVER
                    </CustomButton>
                    <CustomButtonOutline variant="secondary" onClick={handleCloseModalReturn}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                    <Toaster />
                </Modal>
                <Modal
                    show={showModalVerify}
                    onHide={handleCloseModalVerify}
                    backdrop="static"
                    keyboard={false}
                    fullscreen
                >
                    <Modal.Header closeButton>
                    <Modal.Title>ENTREGA DE GUIA</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <TextModal><strong>GUIA:</strong>{' '+selectedElement.guia}</TextModal>
                        <TextModal><strong>DIRECCION:</strong>{` ${selectedElement.direccion} ${selectedElement.numeroExt}, Col. ${selectedElement.colonia}, ${selectedElement.ciudad}, ${selectedElement.estado}. ${selectedElement.codigoPostal}`}</TextModal>
                        <TextModal><strong>TELEFONO:</strong> <a href={`tel:${selectedElement.telefono}`}>{selectedElement.telefono}</a></TextModal>
                        <Alert className="mt-3" variant="warning">Para poder realizar la entrega del paquete es necesario confirmar el numero de guia. Por favor presione escanear para verificarlo.</Alert>
                        <ScanCodeButton onClick={handleVerify}>ESCANEAR</ScanCodeButton>
                        {
                            badGuideMessage && 
                                <Alert variant="danger">
                                    <Alert.Heading>Atencion!</Alert.Heading>
                                    <p>
                                        El numero de guia no corresponde al paquete por entregar. <br/>
                                        Para intentarlo de nuevo presione escanear.
                                    </p>
                                </Alert>
                        }
                        {
                            showMessage &&
                            <QrScan
                                delay={300}
                                onError={handleError}
                                onScan={handleScan}
                                style={{ height: 240, width: 320 }}
                            />
                        }
                    </Modal.Body>
                    <Modal.Footer>
                    <CustomButtonOutline variant="secondary" onClick={handleCloseModalVerify}>
                        CANCELAR
                    </CustomButtonOutline>
                    </Modal.Footer>
                </Modal>
                <Toaster />
            </Container>
        </Layout>
    )
};

export default DriverGuides;