import styled from 'styled-components'
import dayjs, { utc } from 'dayjs';
import { useState, useEffect, useRef } from 'react';
import { Table, InputGroup, FormControl, Row, Col } from 'react-bootstrap';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import PrintIcon from '@material-ui/icons/Print';

import config from '../config'
import '../assets/css/excell-btn.css'

dayjs.extend(utc);

const TableContainer = styled.div`
    margin: auto;
    width: 95vw;
    height: 400px;
    overflow-y: scroll;
    @media (max-width: 860px) {
        width: 780px;
    }
    @media (max-width: 760px) {
        width: 580px;
    }
    @media (max-width: 560px) {
        width: 380px;
    }
    @media (max-width: 360px) {
        width: 280px;
    }
`;

const PrintButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-left: 15px;
    margin-bottom: 10px;
`;

const ReportReturns = () => {
    let fecha = new Date();
    const [data, setData] = useState([]);
    const [selectedInitDate, setSelectedInitDate] = useState(dayjs.utc(fecha).local().format('YYYY-MM-DD'))
    const [selectedFinDate, setSelectedFinDate] = useState(dayjs.utc(fecha).local().format('YYYY-MM-DD'))
    const dateIniRef = useRef(null);
    const dateFiniRef = useRef(null);

    useEffect(() => {
        async function fetchData() {
            let response = await fetch(`${config.urlBase}reportes/devoluciones?iniFecha=${selectedInitDate}&finFecha=${selectedFinDate}`)
            response = await response.json();
            setData(response.data);
        }
        fetchData();
    },[selectedInitDate,selectedFinDate]);

    const handlePrint = () => {
        let contenedor = document.getElementById('reporte-devoluciones').innerHTML
        let w = window.open();
        w.document.write('<html><head>');
        w.document.write('<style>body{writing-mode: rl;}table{width:100%;border-collapse:collapse;margin:16px 0 16px 0;}table th{border:1px solid #ddd;padding:4px;background-color:#d4eefd;text-align:left;font-size:15px;}table td{border:1px solid #ddd;text-align:left;padding:6px;}</style>');
        w.document.write('</head><body >')
        w.document.write(contenedor);
        w.document.write('</body></html>');
        w.document.close(); // necessary for IE >= 10
        w.focus(); // necessary for IE >= 10
		w.print();
		w.close();
        return true;
    }

    const handleChangeDate = async (event) => {
        let dateIni = new Date(dateIniRef.current.value)
        dateIni.setDate(dateIni.getDate()+1)
        let dateFin = new Date(dateFiniRef.current.value)
        dateFin.setDate(dateFin.getDate()+1)
        if (event.target.name === 'iniFecha') {
            setSelectedInitDate(dayjs.utc(dateIni).local().format('YYYY-MM-DD'))   
        }
        if (event.target.name === 'finFecha') {
            setSelectedFinDate(dayjs.utc(dateFin).local().format('YYYY-MM-DD'))
        }
        let response = await fetch(`${config.urlBase}reportes/devoluciones?iniFecha=${dayjs.utc(dateIni).local().format('YYYY-MM-DD')}&finFecha=${dayjs.utc(dateFin).local().format('YYYY-MM-DD')}`)
        response = await response.json();
        setData(response.data);
    }

    return (
        <>
            <h4>REPORTE DE PAQUETES DEVUELTOS A TIENDA</h4>
            <div style={{margin: "15px"}}>
                <ReactHTMLTableToExcel
                    id="btnExportar"
                    table="tabla-devoluciones"
                    filename="testExcel"
                    sheet="Hoja Uno"
                    buttonText="Exportar a excel"
                    className="btn-excell"
                />
                <PrintButton onClick={handlePrint}><PrintIcon />{' '}Imprimir</PrintButton>
            </div>
            <div>
            <Row>
                <Col>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-default">Fecha Inicial</InputGroup.Text>
                        <FormControl
                            type="date"
                            onChange={(e) => handleChangeDate(e)}
                            ref={dateIniRef}
                            value={selectedInitDate}
                            name="iniFecha"
                            max={selectedFinDate}
                        />
                    </InputGroup>
                </Col>
                <Col>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-default">Fecha Final</InputGroup.Text>
                        <FormControl
                            type="date"
                            onChange={(e) => handleChangeDate(e)}
                            ref={dateFiniRef}
                            value={selectedFinDate}
                            name="finFecha"
                            min={selectedInitDate}
                        />
                    </InputGroup>
                </Col>
            </Row>
            </div>
            <TableContainer id="reporte-devoluciones">
                <Table striped bordered hover id="tabla-devoluciones">
                    <thead>
                        <tr>
                            <th>CHOFER DEVOLUCION</th>
                            <th>CLIENTE</th>
                            <th>TIENDA</th>
                            <th>DESTINO</th>
                            <th>MUNICIPIO</th>
                            <th>CP</th>
                            <th># PEDIDO</th>
                            <th># GUIA</th>
                            <th>RECOLECTA TIENDA</th>
                            <th>RECOLECTA TIENDA</th>
                            <th>DEVOLUCION TIENDA</th>
                            <th>DEVOLUCION TIENDA</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data.map(element => (
                                <tr key={element.guia}>
                                    <td>
                                        {element.nombre}
                                    </td>
                                    <td>
                                        {element.nombreCorto}
                                    </td>
                                    <td>
                                        {element.descripcion}
                                    </td>
                                    <td>
                                        {element.direccion}
                                    </td>
                                    <td>
                                        {element.ciudad}
                                    </td>
                                    <td>
                                        {element.codigoPostal}
                                    </td>
                                    <td>
                                        {element.orden}
                                    </td>
                                    <td>
                                        {element.guia}
                                    </td>
                                    <td>
                                        {dayjs.utc(element.fechaRecoleccion).local().format('DD/MM/YYYY')}
                                    </td>
                                    <td>
                                        {dayjs.utc(element.fechaRecoleccion).format('HH:mm A')}
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </Table>
            </TableContainer>
        </>
    )
}

export default ReportReturns;