import styled from "styled-components";
import { useState } from 'react';
import Layout from "../components/Layout";
import TableNewOrder from "../components/TableNewOrder";
import TableAssignOrder from "../components/TableAssignOrder";
import TableCollectedOrder from "../components/TableCollectedOrder";
import TableInWarehouseOrder from "../components/TableInWarehouseOrder";

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Tittle = styled.h3`
    @media (max-width: 340px) {
        font-size: calc(1.3rem + .3vw);
    }
`;

const Box = styled.div`
    width: 100%;
    text-align: end;
    margin-bottom: 25px;
`;

const NavContainer = styled.div`
    width: 90vw;

    overflow-x: scroll;
    ::-webkit-scrollbar {display: none;}
`;

const NavApp = styled.div`
    display: flex;
    align-items: center;
    justify-contetn: space-around;
`;

const NavElement = styled.button`
    background: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-weight: ${props => props.isActive ? "bold;" : "initial;"}

    &:hover {
        font-weight: bold;
    }
`;

const ordersOptions = [
    {id:1,tittle:'NUEVAS',value:'NUEVAS'}, 
    {id:2,tittle:'ASIGNADAS',value:'ASIGNADAS'},
    {id:3,tittle:'RECOLECTADAS',value:'RECOLECTADAS'}, 
    {id:4,tittle:'EN ALMACEN',value:'ALMACEN'}
]

const opciones = {};
opciones["NUEVAS"] = <TableNewOrder />
opciones["ASIGNADAS"] = <TableAssignOrder />
opciones["RECOLECTADAS"] = <TableCollectedOrder />
opciones["ALMACEN"] = <TableInWarehouseOrder />

const Orders = () => {
    const [option, setOption] = useState("NUEVAS");

    const handleOption = (event) => {
        setOption(event.target.name)
    };


    return (
        <Layout>
            <Container>
                <Box>
                    <Tittle>ORDENES DE RECOLECCION</Tittle>
                </Box>
                <NavContainer>
                    <NavApp>
                        {
                            ordersOptions.map(
                                orderOption => (
                                    <NavElement 
                                        key={orderOption.id} 
                                        name={orderOption.value} 
                                        isActive={orderOption.value === option ? true : false}
                                        onClick={handleOption}
                                    >
                                        {orderOption.tittle}
                                    </NavElement>
                                )
                            )
                        }
                    </NavApp>
                </NavContainer>
                {opciones[option]}
            </Container>
        </Layout>
    )
};

export default Orders;