import dayjs from 'dayjs';
import styled from "styled-components";
import { useEffect, useState, useReducer, useRef } from 'react';
import { Table, Form, FloatingLabel, Row, Col, Modal } from 'react-bootstrap';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import NewOrderModal from './Modal';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import toast, { Toaster } from 'react-hot-toast';

import config from '../config';

const headers = [
    {id: 1, description: 'orden'},
    {id: 2, description: 'fecha'},
    {id: 3, description: 'sucursal'},
    {id: 4, description: 'paquetes'},
    {id: 5, description: 'estatus'}
];

const TextBtn = styled.p`
    margin: 0;
`;

const TableContainer = styled.div`
    width: 60%;
    height: 300px;
    overflow-y: scroll;
`;

const TableContainerTwo = styled.div`
    width: 100%;
    height: 350px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
    width: 100%;
`;

const AssignContainer = styled.div`
    position: relative;
    top: -170px;
    width: 30%;
    height: 300px;
    padding: 10px;
    text-align: center;
`;

const Box = styled.div`
    width: 100%;
    text-align: start;
    margin-bottom: 25px;
    margin-top: 25px;
`;

const BtnAdd = styled.button`
    border: none;
    background-color: #fdd448;
    height: 40px;
    font-weight: bold;
    cursor: pointer;
    padding-left: 20px;
    padding-right: 20px;
    
    &:active {
        background-color: #d6b43e;
    }
`;

const OutlineBtn = styled.button`
    border: solid 1px #fdd448;
    background-color: transparent;
    height: 40px;
    font-weight: bold;
    cursor: pointer;
    padding-left: 20px;
    padding-right: 20px;
    color: black;
    
    &:active {
        background-color: #d6b43e;
    }
`;

const BtnDelete = styled.button`
    border: none;
    background: transparent;
    color: #FF2626;
    width: 100%;
`;

const initialState = {
    markedOrders: 0,
    selectedOrders: []
}

function reducer(state, action) {
    switch (action.type) {
        case "add_one":
            return {
                markedOrders: state.markedOrders+1,
                selectedOrders: state.selectedOrders.concat(action.payload)
            }
        case "substract_one":
            return {
                markedOrders: state.markedOrders-1,
                selectedOrders: state.selectedOrders.filter(selectedOrder => selectedOrder.orden !== action.payload.orden)
            }
        case "add_all":
            return {
                markedOrders: action.payload.length,
                selectedOrders: action.payload
            }
        case "substract_all":
            return {
                markedOrders: 0,
                selectedOrders: []
            }
        case "reorder":
            return {
                ...state,
                selectedOrders: action.payload
            }
        default:
            throw new Error();
    }
}

const reorder = (list, startIndex, endIndex) => {
    const result = [...list];
    const [removed] = result.splice(startIndex,1);
    result.splice(endIndex,0,removed);
    return result;
} 

const TableNewOrder = () => {
    const [state, dispach] = useReducer(reducer,initialState);
    const [orders, setOrders] = useState([]);
    const [branches, setBranches] = useState([]);
    const [drivers, setDrivers] = useState([]);
    const [addShow, setAddShow] = useState(false);
    const [warehouses, setWarehouses] = useState([]);
    const [activeWarehouse,setActiveWarehouse] = useState("")
    const choferRef = useRef(null);
    const warehouseRef = useRef(null);
    const allCheckboxRef = useRef(null);
    const [show, setShow] = useState(false);
    const [elementToBeDeleted, setElementToBeDeleted] = useState("");

    const handleClose = () => setShow(false);
    const handleShow = (e) => {
        setElementToBeDeleted(e);
        setShow(true);
    };

    const handleAddClose = () => setAddShow(false);
    const handleAddShow = () => setAddShow(true);

    useEffect(() => {
        async function fetchOrders() {
            let response = await fetch(`${config.urlBase}ordenes/pendientes`);
            response = await response.json();
            setOrders(response.data);
            let responseB = await fetch(`${config.urlBase}sucursales/`);
            responseB = await responseB.json();
            setBranches(responseB.data);
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            responseW = await responseW.json();
            setWarehouses(responseW);
            setActiveWarehouse(responseW[0].almacen);
        }

        fetchOrders()
    },[]);

    useEffect(() => {
        async function fetchDrivers() {
            let response = await fetch(`${config.urlBase}choferes/`)
            response = await response.json()
            setDrivers(response)
        }

        fetchDrivers()
    },[]);

    const handleChange = (event, order) => {
        if (event.target.checked) {
            dispach({type: "add_one", payload: order})
            let ordersBySelectedWarehouse = orders.filter(order => order.almacen === activeWarehouse)
            if((state.selectedOrders.length+1) === ordersBySelectedWarehouse.length) {
                allCheckboxRef.current.checked = true;
            }
        } else {
            dispach({type: "substract_one", payload: order})
            if((state.selectedOrders.length-1) === 0) {
                allCheckboxRef.current.checked = false;
            }
        }
    };

    const handleRow = (event, order) => {
        dispach({type: "substract_one", payload: order})
    }
    
    const handleAllChange = (event, orders) => {
        if (event.target.checked) {
            dispach({type: "add_all", payload: orders})
        } else {
            dispach({type: "substract_all"})
        }
    };
    
    const handlingAssignment = async () => {
        let requests = state.selectedOrders.map(selectedOrder => fetch(`${config.urlBase}ordenes/asignar`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                orden: selectedOrder.orden,
                chofer:choferRef.current.value
            })
        }));
        let responses = await Promise.all(requests);
        console.log(responses)
        let newOrders = orders;
        dispach({type: "substract_all"})
        state.selectedOrders.map(selectedOrder => newOrders = newOrders.filter(order => order.orden !== selectedOrder.orden))
        setOrders(newOrders);
    }

    const handleChangeWarehouse = () => {
        setActiveWarehouse(warehouseRef.current.value)
        dispach({type: "substract_all"})
        allCheckboxRef.current.checked = false;
    }

    const handleDeleteOrder = async (orderNumber) => {
        try {
            let response = await fetch(`${config.urlBase}ordenes/eliminar?orden=${orderNumber}`,{
                method: "DELETE"
            });
            response = await response.json();
            setOrders(orders.filter(order => order.orden !== orderNumber));
            setShow(false);
            toast.success(response.message)
        } catch (error) {
            console.log(error)
            toast.error(error.message)
        }
    }

    return (
        <DragDropContext onDragEnd={result => {
            const {source, destination} = result;
            if (!destination) {
                return;
            }
            if (source.index === destination.index && source.droppableId === destination.droppableId) {
                return;
            }
            let element = reorder(state.selectedOrders,source.index,destination.index)
            dispach({type: "reorder", payload: element})
        }}>
            <Box>
                <BtnAdd onClick={handleAddShow}>
                    <TextBtn>NUEVA ORDEN</TextBtn>
                </BtnAdd>
                <NewOrderModal show={addShow} handleClose={handleAddClose} setOrders={setOrders} orders={orders}/>
            </Box>
            {
                orders.length === 0 && <h1>No se encontraron registros.</h1>
            }
            {
                orders.length > 0 &&
                <Row>
                    <Col xs={3}>
                        <FloatingLabel controlId="floatingSelect" label="Almacen">
                            <Form.Select aria-label="Floating label select example" ref={warehouseRef} onChange={handleChangeWarehouse}>
                                {
                                    warehouses.map(warehouse => <option key={warehouse.almacen} value={warehouse.almacen}>{warehouse.descripcion}</option>)
                                }
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                </Row>
            }
            <div style={{display: 'flex', justifyContent: 'space-around', padding: "20px"}}>
            {
                orders.length > 0 && 
                (
                    <>
                        <TableContainer>
                            <Table hover responsive striped bordered>
                                <thead>
                                    <tr>
                                        <th>
                                            {state.markedOrders > 0 && <small style={{marginRight: 10 + 'px'}}>{state.markedOrders}</small>}
                                            <input type="checkbox" onChange={(event) => handleAllChange(event,orders)} ref={allCheckboxRef}/>
                                        </th>
                                        {headers.map(header => (
                                            <th key={header.id}>{header.description}</th>
                                        ))}
                                        <th>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {orders.map(order => {
                                        if(order.almacen === activeWarehouse) {
                                            return (
                                                <tr key={order.orden}>
                                                    <td>
                                                        <input type="checkbox" checked={state.selectedOrders.includes(order)} onChange={(event) => handleChange(event, order)} name={order.orden}/>
                                                    </td>
                                                    <td>
                                                        {order.orden}
                                                    </td>
                                                    <td>
                                                        {dayjs.utc(order.fecha).format('DD/MM/YYYY')}
                                                    </td>
                                                    <td>
                                                        {branches.map(branch => branch.sucursal === order.sucursal ? branch.descripcion : "")}
                                                    </td>
                                                    <td>
                                                        {order.paquetes}
                                                    </td>
                                                    <td>
                                                        {order.estatus}
                                                    </td>
                                                    <td>
                                                        <BtnDelete onClick={() => handleShow(order.orden)}>
                                                            <DeleteForeverIcon />
                                                        </BtnDelete>
                                                    </td>
                                                </tr>
                                            )
                                        }
                                        return <></>
                                    })}
                                </tbody>
                            </Table>
                        </TableContainer>
                        <Modal show={show} onHide={handleClose}>
                            <Modal.Header closeButton>
                                <Modal.Title>ELIMINAR REGISTRO</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>LA ORDEN {elementToBeDeleted} SERA ELIMINADA. DESEA CONTINUAR?</Modal.Body>
                            <Modal.Footer>
                                <OutlineBtn variant="secondary" onClick={handleClose}>
                                    CANCELAR
                                </OutlineBtn>
                                <BtnAdd variant="primary" onClick={() => handleDeleteOrder(elementToBeDeleted)}>
                                    ELIMINAR
                                </BtnAdd>
                            </Modal.Footer>
                        </Modal>
                        <AssignContainer>
                            {
                                state.markedOrders > 0 &&
                                <CustomButton onClick={handlingAssignment}>
                                    <PersonAddIcon />{' '}
                                    ASIGNAR
                                </CustomButton>
                            }
                            <Form.Group className="mb-3" >
                                <Form.Label><strong>CHOFER:</strong></Form.Label>
                                <Form.Select aria-label="Default select example" ref={choferRef}>
                                    {drivers.map(chofer => <option key={chofer.chofer} value={chofer.chofer}>{chofer.nombre}</option>)}
                                </Form.Select>
                            </Form.Group>
                            <TableContainerTwo>
                                <Table hover responsive striped bordered>
                                    <Droppable droppableId="choferOrdenes">
                                        {(droppableProvider) => (
                                            <tbody
                                                {...droppableProvider.droppableProps}
                                                ref={droppableProvider.innerRef}
                                            >
                                                {
                                                    state.selectedOrders.map((selectedOrder, index) => (
                                                        <Draggable key={selectedOrder.orden} draggableId={`orden${selectedOrder.orden}`} index={index}>
                                                            {
                                                                (draggableProvider) => (
                                                                <tr
                                                                    {...draggableProvider.draggableProps}
                                                                    ref={draggableProvider.innerRef}
                                                                    {...draggableProvider.dragHandleProps} 
                                                                    onClick={(e) => handleRow(e,selectedOrder)}
                                                                >
                                                                    <td>{selectedOrder.orden}</td>
                                                                    <td>{branches.map(branch => branch.sucursal === selectedOrder.sucursal ? branch.descripcion : "")}</td>
                                                                </tr>
                                                                )
                                                            }
                                                        </Draggable>
                                                    ))
                                                }
                                                {droppableProvider.placeholder}
                                            </tbody>
                                        )}
                                    </Droppable>
                                </Table>
                            </TableContainerTwo>
                        </AssignContainer>
                    </>
                )
            }
            </div>
            <Toaster />
        </DragDropContext>
        )
};

export default TableNewOrder;