import dayjs from 'dayjs';
import { useState, useRef } from 'react';
import { Form, Table, Button, InputGroup, FormControl, Row, Col } from 'react-bootstrap';
import styled from 'styled-components';
import toast, { Toaster } from 'react-hot-toast';

import config from '../config';

const Container = styled.div`
    margin-top: 20px;
`;

const GuideTracking = () => {
    const [order, setOrder] = useState(null);
    const orderRef = useRef(null);

    const handleSubmit = async () => {
        try {    
            if(!orderRef) {
                throw Error('Debe ingresar una guia valida')   
            }
            let response = await fetch(`${config.urlBase}guias/consulta/guia?guia=${orderRef.current.value}`);
            response = await response.json()
            if(!response.data) {
                throw Error(response.message)   
            }
            setOrder(response.data);
        } catch (error) {
            setOrder(null);
            toast.error(error.message)
        }
    };

    return (
        <Container>
            <Form.Label><strong>GUIA:</strong></Form.Label>
            <InputGroup className="mb-3">
                <FormControl
                    ref={orderRef} 
                />
                <Button variant="outline-secondary" onClick={handleSubmit}>
                    VALIDAR
                </Button>
            </InputGroup>
            {
                order &&
                <Row>
                    <Col>
                        <h6>ORDEN: {order.orden}</h6>
                    </Col>
                    <Col>
                        <h6>GUIA: {order.guia}</h6>
                    </Col>
                </Row>
            }
            {
                order &&
                <Row>
                    <Col>
                        <Table striped bordered responsive>
                            <thead>
                                <tr>
                                    <th>
                                        estatus
                                    </th>
                                    <th>
                                        fecha
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    order.fechaRecoleccion &&
                                    <tr>
                                        <td>
                                            Recolectada
                                        </td>                                
                                        <td>
                                            {dayjs.utc(order.fechaRecoleccion).local().format('DD/MM/YYYY')}
                                        </td>
                                    </tr>
                                }
                                {
                                    order.fechaEntregaAlmacen &&
                                    <tr>
                                        <td>
                                            Entregada a Almacen
                                        </td>                                
                                        <td>
                                            {dayjs.utc(order.fechaEntregaAlmacen).local().format('DD/MM/YYYY')}
                                        </td>
                                    </tr>
                                }
                                {
                                    order.FechaEntregaChofer &&
                                    <tr>
                                        <td>
                                            En transito
                                        </td>                                
                                        <td>
                                            {dayjs.utc(order.FechaEntregaChofer).local().format('DD/MM/YYYY')}
                                        </td>
                                    </tr>
                                }
                                {
                                    order.fechaDevueltaAlmacen &&
                                    <tr>
                                        <td>
                                            Devuelta a Almacen
                                        </td>                                
                                        <td>
                                            {dayjs.utc(order.fechaDevueltaAlmacen).local().format('DD/MM/YYYY')}
                                        </td>
                                    </tr>
                                }
                                {
                                    order.RecibeFecha &&
                                    <tr>
                                        <td>
                                            Entregado
                                        </td>                                
                                        <td>
                                            {dayjs.utc(order.RecibeFecha).local().format('DD/MM/YYYY')}
                                        </td>
                                    </tr>
                                }
                            </tbody>
                        </Table>
                    </Col>
                </Row>
            }
        <Toaster />
        </Container>
    )
}

export default GuideTracking;