import { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Table, Modal,InputGroup, FormControl, Form } from 'react-bootstrap';

import config from '../config';

const TableContainer = styled.div`
    margin: auto;
    width: 780px;
    height: 300px;
    overflow-y: scroll;
    @media (max-width: 860px) {
        width: 780px;
    }
    @media (max-width: 760px) {
        width: 580px;
    }
    @media (max-width: 560px) {
        width: 380px;
    }
    @media (max-width: 360px) {
        width: 280px;
    }
`;

const DeleteButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: red;
    }
`;

const EditButton = styled.button`
    background: transparent;
    color: black;
    border: none;

    &:hover {
        color: #fdd448;
    }
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CatalogBranch = () => {
    const [elements, setElements] = useState([]);
    const [customers, setCustomers] = useState([]);
    const [warehouses, setWarehouses] = useState([]);
    const [selectElement, setSelectElement] = useState({});
    const [showAddModal, setShowAddModal] = useState(false);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const clienteRef = useRef(null);
    const sucursalRef = useRef(null);
    const descripcionlRef = useRef(null);
    const direccionRef = useRef(null);
    const numeroExtRef = useRef(null);
    const numeroIntRef = useRef(null);
    const coloniaRef = useRef(null);
    const ciudadRef = useRef(null);
    const estadoRef = useRef(null);
    const codigoPostalRef = useRef(null);
    const contactoRef = useRef(null);
    const conTelefonoRef = useRef(null);
    const conEmailRef = useRef(null);
    const almacenRef = useRef(null);

    useEffect(()=> {
        async function fetchData(){
            let responseB = await fetch(`${config.urlBase}sucursales/`);
            responseB = await responseB.json();
            setElements(responseB.data);
            let responseC = await fetch(`${config.urlBase}clientes/`);
            responseC = await responseC.json();
            setCustomers(responseC);
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            responseW = await responseW.json();
            setWarehouses(responseW);
        }
        fetchData()
    },[]);

    const handleCloseAddModal = () => setShowAddModal(false);
    const handleShowAddModal = () => setShowAddModal(true);
    const handleCloseEditModal = () => setShowEditModal(false);
    const handleShowEditModal = (element) => {
        setSelectElement(element);
        setShowEditModal(true);
    };
    const handleCloseDeleteModal = () => setShowDeleteModal(false);
    const handleShowDeleteModal = (element) => {
        setSelectElement(element);
        setShowDeleteModal(true);
    };
    

    const handleDelete = async () => {
        let response = await fetch(`${config.urlBase}sucursales/eliminar?sucursal=${selectElement.sucursal}&cliente=${selectElement.cliente}`,{
            method: 'DELETE'
        });
        let data = await response.json();
        console.log(data);
        setElements(elements.filter(element => element.sucursal !== selectElement.sucursal));
        setShowDeleteModal(false);
    };

    const handleAdd = async () => {
        let newElement = {};
        newElement.cliente = clienteRef.current.value;
        newElement.sucursal = sucursalRef.current.value;
        newElement.descripcion = descripcionlRef.current.value;
        newElement.direccion = direccionRef.current.value;
        newElement.numeroExt = numeroExtRef.current.value;
        newElement.numeroInt = numeroIntRef.current.value;
        newElement.colonia = coloniaRef.current.value;
        newElement.ciudad = ciudadRef.current.value;
        newElement.estado = estadoRef.current.value;
        newElement.codigoPostal = codigoPostalRef.current.value;
        newElement.contacto = contactoRef.current.value;
        newElement.conTelefono = conTelefonoRef.current.value;
        newElement.conEmail = conEmailRef.current.value;
        newElement.almacen = almacenRef.current.value;
        let response = await fetch(`${config.urlBase}sucursales/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        setElements(elements.concat(response.data))
        setShowAddModal(false);
    };

    const handleEditChange = (event) => {
        setSelectElement({
            ...selectElement,
            [event.target.name]: event.target.value
        });
    };

    const handleEdit = async () => {
        let newElement = {};
        newElement.cliente = selectElement.cliente;
        newElement.sucursal = selectElement.sucursal;
        newElement.descripcion = selectElement.descripcion;
        newElement.direccion = selectElement.direccion;
        newElement.numeroExt = selectElement.numeroExt;
        newElement.numeroInt = selectElement.numeroInt;
        newElement.colonia = selectElement.colonia;
        newElement.ciudad = selectElement.ciudad;
        newElement.estado = selectElement.estado;
        newElement.codigoPostal = selectElement.codigoPostal;
        newElement.contacto = selectElement.contacto;
        newElement.conTelefono = selectElement.conTelefono;
        newElement.conEmail = selectElement.conEmail;
        newElement.almacen = selectElement.almacen;
        let response = await fetch(`${config.urlBase}sucursales/`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(newElement)
        });
        response = await response.json();
        let newsElements = elements;
        newsElements.map( element => {
            if (element.sucursal === selectElement.sucursal) {
                element.cliente = response.data.cliente;
                element.sucursal = response.data.sucursal;
                element.descripcion = response.data.descripcion;
                element.direccion = response.data.direccion;
                element.numeroExt = response.data.numeroExt;
                element.numeroInt = response.data.numeroInt;
                element.colonia = response.data.colonia;
                element.ciudad = response.data.ciudad;
                element.estado = response.data.estado;
                element.codigoPostal = response.data.codigoPostal;
                element.contacto = response.data.contacto;
                element.conTelefono = response.data.conTelefono;
                element.conEmail = response.data.conEmail;
                element.almacen = response.data.almacen;
            }
            return element
        })
        setElements(newsElements)
        setShowEditModal(false);
    };

    return (
        <>
            <CustomButton onClick={handleShowAddModal}>AGREGAR</CustomButton>
            <Modal
                show={showAddModal}
                onHide={handleCloseAddModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>AGREGAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Cliente</InputGroup.Text>
                        <Form.Select aria-label="cliente" ref={clienteRef}>
                            {customers.map(customer => (
                                <option key={customer.cliente} value={customer.cliente}>{customer.razonSocial}</option>
                            ))}
                        </Form.Select>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text 
                            id="inputGroup-sizing-sm">Codigo</InputGroup.Text>
                        <FormControl 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            ref={sucursalRef} 
                            maxLength="10"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Descripcion</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={descripcionlRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Almacen</InputGroup.Text>
                        <Form.Select aria-label="cliente" ref={almacenRef}>
                            {warehouses.map(warehouse => (
                                <option key={warehouse.almacen} value={warehouse.almacen}>{warehouse.descripcion}</option>
                            ))}
                        </Form.Select>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Direccion</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={direccionRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Exterior</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={numeroExtRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Interior</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={numeroIntRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Colonia</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={coloniaRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Ciudad</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={ciudadRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Estado</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={estadoRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo Postal</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={codigoPostalRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Contaco</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={contactoRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Telefono</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={conTelefonoRef} maxLength="100"/>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Email</InputGroup.Text>
                        <FormControl aria-label="Descripcion" aria-describedby="inputGroup-sizing-sm" ref={conEmailRef} maxLength="100"/>
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleAdd} >AGREGAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseAddModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showEditModal}
                onHide={handleCloseEditModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>EDITAR REGISTRO</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Cliente</InputGroup.Text>
                        <FormControl 
                            disabled 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            value={customers.map( customer => customer.cliente === selectElement.cliente ? customer.razonSocial : '')}
                            readOnly
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text 
                            id="inputGroup-sizing-sm">Codigo</InputGroup.Text>
                        <FormControl 
                            aria-label="Codigo" 
                            aria-describedby="inputGroup-sizing-sm" 
                            value={selectElement.sucursal}
                            readOnly
                            maxLength="10"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Descripcion</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.descripcion}
                            onChange={handleEditChange}
                            name="descripcion"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Descripcion</InputGroup.Text>
                        <FormControl 
                                disabled 
                                aria-label="Almacen" 
                                aria-describedby="inputGroup-sizing-sm" 
                                value={warehouses.map( warehouse => warehouse.almacen === selectElement.almacen ? warehouse.descripcion : '')}
                                readOnly
                            />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Direccion</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm" 
                            maxLength="100"
                            value={selectElement.direccion}
                            onChange={handleEditChange}
                            name="direccion"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Exterior</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.numeroExt}
                            onChange={handleEditChange}
                            name="numeroExt"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Numero Interior</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.numeroInt}
                            onChange={handleEditChange}
                            name="numeroInt"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Colonia</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.colonia}
                            onChange={handleEditChange}
                            name="colonia"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Ciudad</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"  
                            maxLength="100"
                            value={selectElement.ciudad}
                            onChange={handleEditChange}
                            name="ciudad"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Estado</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.estado}
                            onChange={handleEditChange}
                            name="estado"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Codigo Postal</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.codigoPostal}
                            onChange={handleEditChange}
                            name="codigoPostal"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Contaco</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.contacto}
                            onChange={handleEditChange}
                            name="contacto"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Telefono</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion" 
                            aria-describedby="inputGroup-sizing-sm"
                            maxLength="100"
                            value={selectElement.conTelefono}
                            onChange={handleEditChange}
                            name="conTelefono"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Text id="inputGroup-sizing-sm">Email</InputGroup.Text>
                        <FormControl 
                            aria-label="Descripcion"
                            aria-describedby="inputGroup-sizing-sm" 
                            value={selectElement.conEmail}
                            onChange={handleEditChange}
                            maxLength="100"
                            name="conEmail"
                        />
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleEdit} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseEditModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <Modal
                show={showDeleteModal}
                onHide={handleCloseDeleteModal}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>CONFIRMACION NECESARIA!</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {`Al precionar aceptar, el registro ${selectElement.descripcion} ser elminado permanentemente, desea continuar?`}
                </Modal.Body>
                <Modal.Footer>
                    <CustomButton onClick={handleDelete} >ACEPTAR</CustomButton>
                    <CustomButtonOutline onClick={handleCloseDeleteModal}>
                        CANCELAR
                    </CustomButtonOutline>
                </Modal.Footer>
            </Modal>
            <TableContainer>
                <h3>SCURSALES</h3>
                <Table striped hover responsive>
                    <thead>
                        <tr>
                        <th>Codigo</th>
                        <th>Sucursal</th>
                        <th>Contacto</th>
                        <th>Telefono</th>
                        <th>{" "}</th>
                        <th>{" "}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            elements.length > 0 && elements.map( branch => (
                                <tr key={branch.sucursal}>
                                    <td>
                                        {branch.sucursal}
                                    </td>
                                    <td>{branch.descripcion}</td>
                                    <td>{branch.contacto}</td>
                                    <td>{branch.conTelefono}</td>
                                    <td>
                                        <EditButton onClick={()=>handleShowEditModal(branch)}>
                                            <EditIcon />
                                        </EditButton>
                                    </td>
                                    <td>
                                        <DeleteButton onClick={()=>handleShowDeleteModal(branch)}>
                                            <DeleteIcon />
                                        </DeleteButton>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </Table>
                {
                    elements.length === 0 && <h1>No hay datos por mostrar</h1>
                }
            </TableContainer>
        </>
    )
};

export default CatalogBranch;