import { useState } from 'react';

const useInitialState = () => {
    const [state, setState] = useState({
        visibleMenu: false,
        user: {},
        signedUser: false
    });

    const toggleMenu = payload => {
        setState({
            ...state,
            visibleMenu: !payload
        });
    };

    const signUser = payload => {
        setState({
            ...state,
            user: payload,
            signedUser: true
        })
    };

    return {
        toggleMenu,
        signUser,
        state
    };

};

export default useInitialState;