import dayjs from 'dayjs';
import styled from "styled-components";
import { useEffect, useState, useReducer, useRef } from 'react';
import { Table, Form, Modal, Row, Col, FloatingLabel, FormControl, InputGroup, OverlayTrigger, Popover } from 'react-bootstrap';
import PersonAddDisabledIcon from '@material-ui/icons/PersonAddDisabled';

import config from '../config';

const headers = [
    {id: 1, description: 'orden'},
    {id: 2, description: 'fecha'},
    {id: 3, description: 'sucursal'},
    {id: 4, description: 'paquetes'},
    {id: 5, description: 'estatus'},
    {id: 6, description: 'chofer'}
];

const BoxTwo = styled.div`
    width: 100%;
    margin-bottom: 25px;
`;

const BtnAssign = styled.button`
    display: flex;
    width: 200px;
    justify-content: space-around;
    align-items: center;
    background-color: #fdd448;
    color: white;
    border: none;
    padding: 5px;

    &:active {
        background-color: #d6b43e;
    }

    @media (max-width: 500px) {
        width: 300px;
        margin: 0 auto;
    }
    @media (max-width: 320px) {
        width: 270px;
        margin: 0 auto;
    }
`;

const TextBtn = styled.p`
    margin: 0;
`;

const TableContainer = styled.div`
    height: 300px;
    overflow-y: scroll;
`;

const CustomButton = styled.button`
    background-color: #fdd448;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const CustomButtonOutline = styled.button`
    background-color: transparent;
    border: solid 1.5px #fdd448;
    color: black;
    padding: 10px;
    font-weight: bold;
    align-self: flex-end;
    margin-bottom: 10px;
`;

const Display = styled.div`
    border: solid 1.5px black;
    height: 400px;
    overflow-y: scroll;
`;

const HoverElement = styled.td`
    &:hover {
        cursor: pointer;
    }
`;

const initialState = {
    markedOrders: 0,
    selectedOrders: []
}

function reducer(state, action) {
    switch (action.type) {
        case "add_one":
            return {
                markedOrders: state.markedOrders+1,
                selectedOrders: state.selectedOrders.concat(action.payload)
            }
        case "substract_one":
            return {
                markedOrders: state.markedOrders-1,
                selectedOrders: state.selectedOrders.filter(selectedOrder => selectedOrder.orden !== action.payload.orden)
            }
        case "add_all":
            return {
                markedOrders: action.payload.length,
                selectedOrders: action.payload
            }
        case "substract_all":
            return {
                markedOrders: 0,
                selectedOrders: []
            }
        default:
            throw new Error();
    }
}

const TableAssignOrder = () => {
    const [state, dispach] = useReducer(reducer,initialState);
    const [orders, setOrders] = useState([]);
    const [branches, setBranches] = useState([]);
    const [warehouses, setWarehouses] = useState([]);
    const [activeWarehouse,setActiveWarehouse] = useState("")
    const [drivers, setDrivers] = useState([]);
    const [assignShow, setAssignShow] = useState(false);
    const [modifyShow, setModifyShow] = useState(false);
    const [selectedOrder, setSelectedOrder] = useState({});
    const warehouseRef = useRef(null);
    const allCheckboxRef = useRef(null);
    const amountRef = useRef(null);

    const handleAssignClose = () => setAssignShow(false);
    const handleAssignShow = () => setAssignShow(true);

    const handleModifyClose = () => setModifyShow(false);
    const handleModifyShow = element => {
        setSelectedOrder(element)
        setModifyShow(true)
    }

    useEffect(() => {
        async function fetchOrders() {
            let response = await fetch(`${config.urlBase}ordenes/asignadas/all`)
            response = await response.json()
            setOrders(response.data)
            let responseW = await fetch(`${config.urlBase}almacenes/`);
            responseW = await responseW.json();
            setWarehouses(responseW);
            setActiveWarehouse(responseW[0].almacen);
            let responseB = await fetch(`${config.urlBase}sucursales/`);
            responseB = await responseB.json()
            setBranches(responseB.data)
            let responseD = await fetch(`${config.urlBase}choferes/`)
            responseD = await responseD.json()
            setDrivers(responseD)
        }

        fetchOrders()
    },[]);

    const handleChange = (event, order) => {
        if (event.target.checked) {
            dispach({type: "add_one", payload: order})
        } else {
            dispach({type: "substract_one", payload: order})
        }
    };
    
    const handleAllChange = (event, orders) => {
        if (event.target.checked) {
            let filteredOrders = orders.filter(o => o.almacen === activeWarehouse)
            dispach({type: "add_all", payload: filteredOrders})
        } else {
            dispach({type: "substract_all"})
        }
    };
    
    const handlingUnassignment = async () => {
        let promises = state.selectedOrders.map(sO => fetch(`${config.urlBase}ordenes/desasignar`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                orden: sO.orden
            })
        }))
        let responses = await Promise.all(promises);
        console.log(responses)
        let newOrders = orders;
        dispach({type: "substract_all"})
        state.selectedOrders.map(selectedOrder => newOrders = newOrders.filter(order => order.orden !== selectedOrder.orden))
        setOrders(newOrders);
    }

    const handleChangeWarehouse = () => {
        setActiveWarehouse(warehouseRef.current.value)
        dispach({type: "substract_all"})
        allCheckboxRef.current.checked = false;
    }

    const handleEditOrder = async () => {
        try {
            if(!amountRef.current.value) {
                throw Error('La cantidad de paquetes no puede quedar vacia')
            }
            let response = await fetch(`${config.urlBase}ordenes/`,{
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json'
                  },
                body: JSON.stringify(selectedOrder)
            })
            response = await response.json();
            if (response.type === "Error") {
                throw Error(response.message)
            }
            orders.map(order => {
                if (order.orden === selectedOrder.orden) {
                    order.paquetes = selectedOrder.paquetes
                }
            })
            setOrders(orders)
            handleModifyClose();
        } catch (error) {
            console.log(error)
        }
    }

    const handleChangeAmount = () => {
        setSelectedOrder({
            ...selectedOrder,
            paquetes: amountRef.current.value
        })
    }

    return (
        <>
                <Row>
                {
                    orders.length > 0 &&
                    <Col xs={3}>
                        <FloatingLabel controlId="floatingSelect" label="Almacen" className="mb-4 mt-4">
                            <Form.Select aria-label="Floating label select example" ref={warehouseRef} onChange={handleChangeWarehouse}>
                                {
                                    warehouses.map(warehouse => <option key={warehouse.almacen} value={warehouse.almacen}>{warehouse.descripcion}</option>)
                                }
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                }
                   {/*<Col>
                        <FloatingLabel controlId="floatingSelect" label="Numero de Orden" className="mb-4 mt-4">
                            <FormControl type="number" min="1" ref={orderNumberRef} onChange={handleFiltered}/>
                        </FloatingLabel>
                   </Col>*/}
                </Row>
            {state.markedOrders > 0 &&
                <BoxTwo>
                    <BtnAssign onClick={handleAssignShow}>
                        <PersonAddDisabledIcon />
                        <TextBtn>DESASIGNAR</TextBtn>
                    </BtnAssign>
                    <Modal show={assignShow} onHide={handleAssignClose} backdrop="static">
                        <Modal.Header closeButton>
                        <Modal.Title>DESHACER ASIGNACION DE CHOFER</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3" >
                                    <Form.Label>Ordenes que seran marcadas como nuevas:</Form.Label>
                                    <Display>
                                        <Table responsive striped bordered>
                                            <tbody>
                                                {state.selectedOrders.map(selectedOrder => (
                                                    <tr key={selectedOrder.orden}>
                                                        <td>
                                                            <strong>{selectedOrder.orden}</strong>
                                                        </td>
                                                        <td>
                                                            {branches.map(branch => branch.sucursal === selectedOrder.sucursal ? branch.descripcion : "")}
                                                        </td>
                                                    </tr>
                                                    ))}
                                            </tbody>
                                        </Table>
                                    </Display>
                                </Form.Group>
                            </Form>
                        </Modal.Body>
                        <Modal.Footer>
                        <CustomButtonOutline onClick={handleAssignClose}>
                            CERRAR
                        </CustomButtonOutline>
                        <CustomButton onClick={handlingUnassignment}>
                            DESASIGNAR
                        </CustomButton>
                        </Modal.Footer>
                    </Modal>
                </BoxTwo>
            }
            {
                orders.length === 0 && <h1>No se encontraron registros.</h1>
            }
            {
                orders.length > 0 && 
                (
                    <TableContainer>
                        <Table hover responsive striped bordered>
                            <thead>
                                <tr>
                                    <th>
                                        {state.markedOrders > 0 && <small style={{marginRight: 10 + 'px'}}>{state.markedOrders}</small>}
                                        <input type="checkbox" onChange={(event) => handleAllChange(event,orders)} ref={allCheckboxRef}/>
                                    </th>
                                    {headers.map(header => (
                                        <th key={header.id}>{header.description}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {orders.map(order => {
                                    if(order.almacen === activeWarehouse){
                                        return (
                                            <tr key={order.orden}>
                                                <td>
                                                    <input type="checkbox" checked={state.selectedOrders.includes(order)} onChange={(event) => handleChange(event, order)} name={order.orden}/>
                                                </td>
                                                <td>
                                                    {order.orden}
                                                </td>
                                                <td>
                                                    {dayjs.utc(order.fecha).format('DD/MM/YYYY')}
                                                </td>
                                                <td>
                                                    {branches.map(branch => branch.sucursal === order.sucursal ? branch.descripcion : "")}
                                                </td>
                                                <OverlayTrigger
                                                    placement="top"
                                                    overlay={
                                                        <Popover>
                                                            <Popover.Body>
                                                                clic para editar cantidad de paquetes
                                                            </Popover.Body>
                                                        </Popover>
                                                    }
                                                >
                                                    <HoverElement onClick={() => handleModifyShow(order)}>
                                                        {order.paquetes}
                                                    </HoverElement>
                                                </OverlayTrigger>
                                                <td>
                                                    {order.estatus}
                                                </td>
                                                <td>
                                                    {drivers.map(driver => driver.chofer === order.choferRecoleccion ? driver.nombre : "")}
                                                </td>
                                            </tr>
                                        )
                                    } else {
                                        return <></>
                                    }
                                })}
                            </tbody>
                        </Table>
                        <Modal show={modifyShow} onHide={handleModifyClose} backdrop="static">
                        <Modal.Header closeButton>
                        <Modal.Title>MODIFICACION DE PAQUETES</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <InputGroup className="mb-3">
                                <InputGroup.Text id="inputGroup-sizing-default">Orden</InputGroup.Text>
                                <FormControl
                                    readOnly
                                    value={selectedOrder.orden}
                                />
                            </InputGroup>
                            <InputGroup className="mb-3">
                                <InputGroup.Text id="inputGroup-sizing-default">Paquetes</InputGroup.Text>
                                <FormControl
                                    type="number"
                                    onChange={handleChangeAmount}
                                    value={selectedOrder.paquetes}
                                    ref={amountRef}
                                />
                            </InputGroup>
                        </Modal.Body>
                        <Modal.Footer>
                        <CustomButtonOutline onClick={handleModifyClose}>
                            CANCELAR
                        </CustomButtonOutline>
                        <CustomButton onClick={handleEditOrder}>
                            ACEPTAR
                        </CustomButton>
                        </Modal.Footer>
                    </Modal>
                    </TableContainer>
                )
            }
        </>
        )
};

export default TableAssignOrder;